$quote-themes: (
    corporate: (
        $color-alizarin,
    ),
    undergraduate: (
        $color-ug-lead,
    ),
    masters: (
        $color-msc-lead,
    ),
    phd: (
        $color-phd-lead,
    ),
    mba: (
        $color-mba-accent,
    ),
    online-msc: (
        $color-msc-accent,
    ),
    emba: (
        $color-mba-accent,
    ),
    demba: (
        $color-mba-accent,
    ),
    centres: (
        $color-centres-bright,
    ),
    executive-education: (
        $color-exec-lead,
    ),
);

@each $theme, $colors in $quote-themes {
    $quote-color: nth($colors, 1);

    .scp--#{$theme} {
        blockquote {
            &::before {
                background-color: $quote-color;
            }
        }
    }
}

/*==Fallback styles for blockquotes that haven't had their markup updated to included wrapper div*/
blockquote {
    padding: $measure-large $measure-large $measure-large $measure-large;
    position: relative;

    &::before {
        font-family: $font-family-meta;
        content: ('');
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        display: block;
        margin-bottom: $measure-large;
        width: 180px;
        height: 40px;
        background-color: black;
        mask: url('../img/quote.svg') no-repeat;
        mask-size: 100%;
    }

}



.blockquote__wrap {
    @include bracket-border-v23();
    display: inline-block;
    padding:$measure-xlarge;
    margin-bottom: $measure--basic;
    p:last-child{
        margin-bottom:0;
    }

    blockquote {
        /*remove fallback styles when structure contains wrapper class*/
        padding:0;
        &::before {
            content: none;
        }
    }
}
blockquote {
    position: relative;


    & p {
        font-family: $font-family-meta;
        @include heading-font-features();
        font-size: $font-size-delta;
    }

    & .reference {
        color: $color-nobel;
        font-size: $font-size-default;
    }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    /* IE10+ CSS styles go here */
    blockquote::before {
        content: url('../img/quote.svg') !important;
        background-color: transparent !important;
    }
}

/* IE Edge 12+ CSS styles go here */
@supports (-ms-ime-align: auto) {
    blockquote::before {
        content: url('../img/quote.svg') !important;
        background-color: transparent !important;
    }
}
