/*Animation keyframes*/

@keyframes revealSlideUp {
    0% {
        opacity: 0;
        transform: translateY(2rem);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes reveal {
    0% {
        transform: scaleY(1);
    }
    100% {
        transform: scaleY(0);
    }
}

@keyframes lines {
    0% {
        transform: scaleX(0);
    }
    50% {
        transform: scaleX(1);
    }
    100% {
        transform: scaleX(0);
    }
}
  
@keyframes line-left {
    0% {
        transform-origin: 100% 50%;
    }
    50%, 100% {
        transform-origin: 0% 50%;
    }
}
  
@keyframes line-right {
    0% {
        transform-origin: 0% 50%;
    }
    50%, 100% {
        transform-origin: 100% 50%;
    }
}

@keyframes bracket {
    0% {
        transform: scaleY(0);
    }
    100% {
        transform: scaleY(1);
    }
}
  
@keyframes bracket-line {
    0% {
        transform: scaleX(0);
    }
    100% {
        transform: scaleX(1);
    }
}

.bayes-home{
    position: relative;

    .bayes-video-banner__banner{
        &:before{
            height: 100%;
        }

        video{
            display: block;
        }
    }
    
    .animate{
        .bayes-video-banner__text__wrapper{
            animation: revealSlideUp ease-in 2s;
        }
    }

    .bayes-video-banner__text__wrapper{
        margin-top: 10%;

        .video-banner__text{
            position: relative;
            .video-banner__text__title{
                text-shadow: 1px 2px rgba(0, 0, 0, 0.3);
            }
        }

        @include media('>extralarge'){
            left: calc((100% - 1440px)/2 + 1rem);
            .video-banner__text{
                margin: initial;
            }
        }

        @include media('>=medium','<=extralarge'){
            left: $grid-padding;
            .video-banner__text{
                margin: initial;
            }
        }

        @include media('<medium'){
            margin-top: initial;
            height: 100%;
            width: 100%;

            .video-banner__text{
                padding-right: 2rem;
                padding-left: 2rem;
            }
        }
    }

    .image-banner{

        .yt-video__cta{
            display: flex;
            align-items: center;

            .yt-video__cta__play-lable{
                @include anchor-reset();
                margin: unset;
                color: $color-white;
                margin-left: $measure-medium;

            }
        }

        .image-banner__text__wrapper--title {
            width: 80%;
            position: relative;
            margin-left: auto;
            margin-right: auto;
            padding-left: $measure--small4;
            padding-right: $measure--small4;

            @include media('<mobile'){
                width: 85%;
                padding-left: $measure--small2;
                padding-right: $measure--small2;
            }

            h2{
                font-weight: $font-weight-bold;
                font-size: 2.25rem;
                line-height: 3rem;
                text-shadow: 1px 2px rgba(0, 0, 0, 0.3);
            }
        }

        .image-banner__text__title {
            text-shadow: 1px 2px rgba(0, 0, 0, 0.3);
            @extend %alpha;
            @include font-family(meta);
            color: $color-lightgrey;
            font-weight: $font-weight-bold;
            
            @include media('<mobile'){
                font-size: $font-size-gamma;
                font-weight: $font-weight-black;
            }
        }
    }

    .bayes-youtube-embed-container{
        &:before{
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background-color: $color-black;
        }
    }
}