.bayes-statistic-v24{
    position: relative;
    background-color: $color-black;
    color: $color-white;
    padding-top: $measure--medium3;

    @include media('<=between'){
        padding-bottom: $measure--small4;
        padding-top: $measure--small4;
    }
}

.bayes-statistic-v24--light{
    background-color: $color-gallery;
    color: $color-black;
}

.bayes-statistic-v24__wrapper{
    width: 100%;

    h2{
        font-size: 2rem;
        font-weight: bold;
        line-height: 1.56;
    }
}

.bayes-statistic-v24__block{
    padding-bottom: $measure--medium3;
    @include media('>tablet'){
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: $measure--small2;
    }

    li{
        display: flex;
        justify-content: start;
        align-items: flex-start;

        @include media('<=tablet'){
            padding-bottom: $measure-medium;
        }
    }
}

.bayes-statistic-v24__block__icon{
    max-width: $measure--medium7;
    margin-right: $measure-medium;
    width: 100%;

    &:before{
        display: block;
        padding: $measure--half;
    }
}

.bayes-statistic-v24__block__text{
    display: flex;
    flex-direction: column;
}

.bayes-statistic-v24__block__text--title{
    @include font-family(meta);
    font-weight: $font-weight-medium;
    line-height: 1;
    font-size: $measure--small1;
}

.bayes-statistic-v24__block__text--description{
    p{
        margin-bottom: unset;
    }
    
    a{
        @include anchor-reset();
        border-bottom: $border-width-hairline solid $color-white;

        &:focus,
        &:active{
            outline: $border-width-outline $border-style-default $color-white;
            outline-offset: 0.1875rem;
        }

        &:active{
            outline-style: dotted;
        }

        &:hover,
        &:focus,
        &:active{
            background-color: $color-white;
            color: $color-black;
        }
    }
}

.bayes-statistic-v24--light{
    .bayes-statistic-v24__block__text--description{
        a{
            border-color: $color-black;
    
            &:focus,
            &:active{
                outline-color: $color-black;
            }
    
            &:hover,
            &:focus,
            &:active{
                background-color: $color-black;
                color: $color-white;
            }
        }
    }
}