.bayes-home{
    & > section{
        padding-top: $measure--medium7;
        @include full-screen;

        @include media('<=small') {
            padding-left: $measure--basic;
            padding-right: $measure--basic;
        }

        @include media('>medium') {
            display: grid;
            @include x-column-grid(18);
        }
    }

    .bayes-full-width-container{
        padding-top: $measure--medium7;
    }
}

.bayes-news-v24,
.bayes-news-v24__list,
.bayes-statistic-v24,
.block-links-v24,
.bayes-events-v24,
.bayes-events-v24__lists{
    @include media('>medium') {
        grid-column: 1/-1;
    }
}


.bayes-home{
    .bayes-news-v24,
    .bayes-events-v24
    {
        @include media('<=small') {
            padding-left: $measure--half;
            padding-right: $measure--half;
        }
    }

    .bayes-news-v24__list{
        padding-top: $measure--basic;

        @include media('>mobile') {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-column-gap: $measure--small2;
            padding-top: $measure--small4;

            .feature-news__card{
                width: fit-content;
                padding-left: initial;
                padding-right: initial;
            }
        }

        @include media('>medium') {
            grid-template-columns: repeat(3, 1fr);
        }
    }
  
    .bayes-statistic-v24{
        @include media('<=small') {
            padding-left: $measure--basic;
            padding-right: $measure--basic;
        }
        
        @include media('>small') {
            display: grid;
            @include x-column-grid(18);
            grid-row-gap: $measure--small2;

            h2{
                grid-column: 3/-3;
            }

            .bayes-statistic-v24__block{
                grid-column: 3/-3;
            }
        }
      }

    .bayes-events-v24__lists{
        padding-top: $measure--small4;
        padding-bottom: $measure--small4;
        
        @include media('>medium') {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-column-gap: $measure--small2;
        }
    }
}