////
/// Content transitions V23
/// @group components
////
/// 

// Vertical content transition

.content-transition-vertical--start {
    opacity: 0;
    top: 1rem;
    transition: opacity 0.6s ease, top 0.4s ease;
    position: relative;
}

.content-transition-vertical--end {
    opacity: 1;
    top: 0;
}

// Horizontal content transition - 2 versions, normal & slow

.content-transition-horizontal--start {
    opacity: 0;
    left: -1rem;
    transition: opacity 0.5s ease, left 0.5s ease;
    position: relative;
}

.content-transition-horizontal--slow--start {
    opacity: 0;
    left: -1rem;
    transition: opacity 1.5s ease, left 1.5s ease;
    position: relative;
}

.content-transition-horizontal--end {
    opacity: 1;
    left: 0;
}

// Square overlay transition over image

.square-animation-v23 {
    position: relative;
    overflow: visible;

    @include media('>medium') {
        padding-bottom: $measure--medium3;
    }

    .square-animation-v23__wrap  {
        position: absolute;
        height: 115%;
        width: 100%;
        top: 0;
        overflow: hidden;
        align-items: center;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(25%, 1fr));

        @include media('<=medium') {
            height: 100%;
        }

        .square-animation-v23__square {
            @include media('>between') {
                justify-content: center;
                align-items: center;
                display: flex;
                width: 100%;
                aspect-ratio: 1 / 1;

                &:before {
                    content: '';
                    height: 0.75rem;
                    width: 0.75rem;
                    display: inherit;
                    opacity: 0;
                    
                    @include media('<medium') {
                        height: 1rem;
                        width: 1rem;
                    }
                }
            }
        }

        .square-animation-v23__animation-started {
            &:before {
                animation: square-twinkle-animation ease-out 10s infinite;
            }
        }
    }
}

@each $level, $color in $courses-v23-palette {
    $tile-color: nth($color, 23);

    .#{$level}-courses-v23 {
        .square-animation-v23__square:before {
            background: $tile-color;
        }
    }
}

@keyframes square-twinkle-animation {
    0% {opacity: 0.5; scale: 0}
    10% {opacity: 0.5; scale: 0}
    15% {opacity: 0.5; scale: 1}
    20% {opacity: 0.5; scale: 1}
    30% {opacity: 0.75}
    40% {opacity: 1}
    50% {opacity: 1}
    60% {opacity: 1}
    70% {opacity: 0.75}
    80% {opacity: 0.5; scale: 1}
    85% {opacity: 0.5; scale: 1}
    90%{opacity: 0.5; scale: 0}
    100% {opacity: 0.5; scale: 0}
}         