.dropdown-filter {
    .data-specific {
        display: none;
    }

    .filter--box {
        @include styled-dropdown();
    }

    .data-group {
        #Selectacountry {
            display: none;
        }
        margin: 20px 0 40px;
        .data-specific {
            box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
            padding: 20px 20px 15px;
        }
    }
}

.dropdown-filter__v23 {
    .dropdown-heading{
        @include h3style();
    }

    .dropdown-filter {
        .data-group{
            .data-specific{
                box-shadow: initial;
                padding: initial;
            }
        } 
    }
}
