////
/// accordion-v23
/// @group patterns
////

@mixin accordion() {
    margin-bottom: 0;

    // Heading focus/active needed for Firefox and Safari. Other interaction states on button
    &:focus {
        border: 0;
    }
    &:active {
        border: 0;
        outline: 0;
    }

    border: $border-width-default $border-style-default transparent;
    border-bottom-color: $color-gray80;

    button {
        width: 100%;
        text-align: left;
        padding: $measure--basic;
        color: $color-black;

        div {
            grid-column-gap: 0;
            display: flex;
            align-items: center;
            gap: $measure--basic;
        }

        .accordion-v23__heading__indicator {
            background-color: transparent;
            color: $color-black;
            width: $measure--small4;
            height: $measure--small4;
            border-width: 0;
            position: relative;
            cursor: pointer;
            display: inline-block;
            border: $border-width-default $border-style-default $color-black;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;

            &::before,
            &::after {
                content: '';
                background-color: currentColor;
                width: 0.2rem;
                height: $measure--basic;
                display: block;
                position: absolute;
                margin: $measure--half;
                transition: transform 0.4s ease;
            }

            &::before {
                transform: rotate(90deg);
            }
        }

        &:hover {
            background-color: $color-white;

            & .accordion-v23__heading__text {
                text-decoration: underline;
            }
        }
        &:focus {
            outline: $border-width-outline $border-style-default $color-black;
            outline-offset: $border-width-thick;
            background-color: $color-white;
            position: relative;
            z-index: z-index(default);
        }
        &:active {
            outline: $border-width-outline $border-style-alternate $color-black;
            outline-offset: $border-width-thick;
            background-color: $color-white;

            & .accordion-v23__heading__text {
                text-decoration: underline;
            }
        }
    }

    &[data-open='true'] {
        border: $border-width-default $border-style-alternate $color-black;
        border-bottom: 0;
        &:focus,
        &:active {
            outline: 0;
        }

        button {
            text-decoration: underline;
            background-color: $color-white;

            .accordion-v23__heading__indicator {
                &::after {
                    transform: rotate(90deg);
                }
            }
        }

        & + .accordion-v23__body {
            border: $border-width-default $border-style-alternate $color-black;
            border-top: none;

            p {
                margin: $measure--basic 0;
            }

            & > :first-child {
                margin-top: 0;
            }

            & > :last-child {
                margin-bottom: 0;
            }
        }

        & + .accordion-v23__body:first-child {
            margin-top: 0;
        }
    }
}

.js .accordion-v23 {
    @include media('screen') {
        .accordion-v23__heading {
            &:last-child {
                button {
                    border-bottom-color: transparent;
                }
            }

            &[data-open='true'] {
                border: $border-width-default $border-style-default transparent;
            }
        }

        .accordion-v23__body {
            padding: $measure--half $measure--basic $measure--basic
                $measure--basic;

            //nested accordions
            .accordion-v23 {
                .accordion-v23__heading {
                    button {
                        background-color: $color-gallery;
                        border-bottom: $border-width-default $border-style-default $color-white;

                        &:hover,
                        &:focus,
                        &:active {
                            background-color: $color-gallery;
                        }

                        .accordion-v23__heading__indicator {
                            transform: rotate(180deg);
                            &:before {
                                content: unquote('"#{ $fa-var-chevron-down }"');
                                @include font-family(icon);
                                height: initial;
                                width: initial;
                                background-color: initial;
                            }
                            &:after {
                                content: none;
                            }
                        }
                    }

                    &[data-open='true'] {
                        button {
                            background-color: $color-gallery;
                            border-bottom: none;
                        }

                        .accordion-v23__heading__indicator {
                            &::before {
                                transform: rotate(180deg);
                            }
                        }

                        & + .accordion-v23__body {
                            background-color: $color-gallery;
                        }
                    }
                }
                &[data-downchevronstyle='true'] {
                    margin-top: initial;
                    margin-bottom: initial;

                    .accordion-v23__heading__label {
                        padding-left: $measure--half;
                        padding-right: $measure--half;
                        background-color: $color-emerald-ice;
                        color: $color-black;
                    }

                    .accordion-v23__heading {
                        @include alphatype();
                        font-weight: $font-weight-bold;
                        border: $border-width-hairline $border-style-default
                        $color-black;
                        margin-bottom: $measure--two-tenths;

                        button {
                            text-decoration: initial;
                            border-bottom: initial;
                            background-color: initial;
                            padding: $measure--quarter;
                            border: initial;

                            &:focus {
                                background-color: $color-whisper;
                                color: $color-black;

                                .accordion-v23__heading__description {
                                    p {
                                        color: $color-black;
                                    }
                                }

                                .accordion-v23__heading__indicator {
                                    &:before {
                                        color: $color-black;
                                    }
                                }
                            }

                            .accordion-v23__heading__description {
                                font-weight: $font-weight-medium;

                                p {
                                    @include smalltype();
                                    margin: initial;
                                    color: $color-lucifer;
                                    line-height: $line-height-ratio--override--gamma;
                                }
                            }

                            div:not(.accordion-v23__heading__description) {
                                display: flex;
                                position: relative;
                                column-gap: initial;

                                .accordion-v23__heading__text {
                                    order: 2;
                                }

                                .accordion-v23__heading__label {
                                    order: 1;
                                    @include smalltype();
                                    margin-right: $measure--half;
                                }

                                :last-child {
                                    position: absolute;
                                    right: 0;
                                    top: 0;
                                    height: 100%;
                                }
                            }

                            .accordion-v23__heading__indicator {
                                transform: initial;
                                border: initial;
                                border-radius: initial;

                                &::before {
                                    transform: initial;
                                }
                            }
                        }

                        &[data-open='true'] {
                            margin-bottom: 0;
                            background-color: $color-whisper;
                            border: $border-width-default $border-style-alternate $color-black;
                            border-bottom: 0;

                            button {
                                background-color: $color-whisper;
                                color: $color-black;

                                .accordion-v23__heading__description {
                                    display: none;
                                }
                            }

                            & + .accordion-v23__body {
                                @include smalltype();
                                line-height: $line-height-ratio--override--delta;
                                background-color:$color-whisper;
                                color: $color-black;
                                margin-bottom: initial;
                                border: initial;
                                padding: $measure--half $measure--quarter $measure--quarter
                                    $measure--quarter;
                                margin-bottom: $measure--two-tenths;
                                border: $border-width-default $border-style-alternate $color-black;
                                border-top: none;

                                p,
                                ul li {
                                    @include smalltype();
                                    line-height: $line-height-ratio--override--delta;
                                }

                                .youtube-video__caption-element--text,
                                .youtube-video__caption-element--time {
                                    @include smalltype();
                                    color: $color-white;
                                }
                            }

                            .accordion-v23__heading__indicator {
                                color: $color-black;

                                &::before {
                                    transform: rotate(180deg);
                                }
                            }
                        }
                    }
                }
            }
        }

        .accordion-v23__heading {
            @include accordion();

            &[data-open='true'] {
                & + .accordion-v23__body {
                    margin-bottom: $measure--basic;

                    .wrapper--accordion-v23__body__content {
                        & p:first-of-type {
                            margin-top: 0;
                        }
                    }
                }
            }

            & + .accordion-v23__body {
                overflow-y: hidden;
                transition-duration: $transition-duration;
                transition-property: height;
                transition-timing-function: $transition-function;

                &[data-closed='true'] {
                    display: none;
                }
            }
        }
    }
}

// course specific override for accordions
.js [class*="-courses-v23"]{
    .accordion-v23 {
        .accordion-v23__heading:first-child button {
            border-top: $border-width-default $border-style-default
                $color-gray80;
        }

        .accordion-v23__heading:last-child button {
            border-bottom: $border-width-default $border-style-default
                $color-gray80;
        }

        .accordion-v23__heading[data-open='true'] /*button*/ {
            border-bottom: none;
        }
    }

    .courses-v23__structure__content
        > .accordion-v23
        > h4.accordion-v23__heading {
        @include h3style();
    }
}
