.embla {
    max-width: 60rem;
    margin: auto;
    --slide-height: 19rem;
    --slide-spacing: 1rem;
    --slide-size: 100%;
    max-width: 100%;

    @include media('>=small') {
        --slide-size:50%;
    }
    @include media('>=tablet') {
        --slide-size: 33%;
    }
    @include media('>=medium') {
        --slide-size: 25%;
    }
    // @include media('<=tablet') {
    //     --slide-size: 33%;
    // }
    
  }
.courses-v23__advisory-board {
    background-color:#F1F1F4 ;
}

  .embla__viewport {
    width: 100%;

    @include media('>=small') {
        // width:67%;
         width: 90%;
    }
    @include media('>=tablet') {
        // width:75%;
    }
    @include media('>=medium') {
        width: 96%;
    }
    @include media('>=extralarge') {
        width: 100%;
    }

    
    // overflow: hidden;
  }
  
  .embla__container {
    display: flex;
    touch-action: pan-y pinch-zoom;
    margin-left: calc(var(--slide-spacing) * -1);
  }
  .embla__slide {
    transform: translate3d(0, 0, 0);
    flex: 0 0 var(--slide-size);
    min-width: 0;
    padding-left: var(--slide-spacing);
    opacity: 0.5;
    transition: opacity 600ms ease-out, transform 600ms ease-out;
    //transform: scale(0.9);
  }


.is-in-view, .is-snapped {
    opacity: 1;
    transform: none;
    transition: opacity 600ms ease-out, transform 600ms ease-out;
    //transform: scale(1);
}
.profile-slider__btn {
    position: relative;
    background: $color-black;
    color:$color-white;
    width: $measure--medium2;
    height: $measure--medium2;
    display: flex;
    justify-content: center;
    align-items: center;
   &:active {
        outline: 2px dotted $color-black;
        outline-offset: 3px;
    }

    &:focus {
        outline: 3px solid $color-black;
        outline-offset: 3px;
    }

    &[disabled] {
        opacity: 0.5;
    }
}

.profile-slider__btn--next{
    // right: -$measure--medium5;
    // @include media('<=extralargePlusSides') {
    //     right: $measure-medium;
    // }
}
.profile-slider__btn--prev {
    // left: -$measure--medium5;
    // @include media('<=extralargePlusSides') {
    //     left: $measure-medium;
    // }
}
.profile-slider__btns {

    // transform: translateY(-50%);
    // width: 100%;
    display: flex;
    justify-content: flex-end;
    // gap: $measure-small;
    margin-bottom: $measure-small;
    @include media('>=medium') {
        position: absolute;
        top: -75px;
        right:0;
        margin-bottom:0;
    }
}
.profile-slider__wrap {
    position: relative;
}
.profile-slider__container {
    position: relative;
    // display: flex;
    // align-items: center;
    // justify-content: flex-start;
    overflow: hidden;
    @include media('>=extralarge') {
        overflow: visible;
    }
}
.profile-slider__slide {
    max-width: none !important;
}
.profile-slider__img {
    margin-bottom: $measure-small;
}
.profile-slider__details {
    margin-bottom: $measure-small;
}
.profile-slider__name {
    font-weight: $font-weight-bold;
}
.profile-slider__company {
    font-weight: $font-weight-bold;
}

.profile-slider__description {
    @include minitype();
    p {
        @include minitype();
    }
}
@each $level, $color in $courses-v23-palette {
    $text-color: nth($color, 3);

    .#{$level}-courses-v23 {
        .profile-slider__company {
            color:$text-color;
        }
    }
}