.ksp__items {
    justify-content: center;
}

.ksp__item {
    text-align: center;
    padding-top: $measure-large;
    padding-bottom: $measure-medium;
    border-bottom: 2px #f2f2f2 solid;

    @include grid-column-map(
        (
            'small': 6,
            'medium': 3,
            'large': 3,
        )
    );

    .ksp__items--2 & {
        @include media('>medium') {
            @include grid-column-map(
                (
                    'small': 6,
                    'medium': 6,
                )
            );
        }
    }

    .ksp__items--3 & {
        @include media('>medium') {
            @include grid-column-map(
                (
                    'small': 6,
                    'medium': 6,
                    'large': 4,
                )
            );
        }
    }

    .ksp__items--4 & {
        @include media('>medium') {
            @include grid-column-map(
                (
                    'small': 6,
                    'medium': 6,
                    'large': 3,
                )
            );
        }
    }

    @include media('>small') {
        border-bottom: none;
        border-right: 2px #f2f2f2 solid;

        &:last-child {
            border: none;
        }
    }

    @include media('<=large') {
        &:nth-child(2) {
            border-right: none;
        }
    }
}

.ksp__item__figure {
    font-size: 70px;
    color: $color-alizarin;
    font-weight: $font-weight-thin;
    margin: 0;
    line-height: 1;
    white-space: nowrap;

    .animate--number__unit {
        font-size: 0.5em;
        display: inline-block;
        vertical-align: middle;
        padding: 0 ($measure-small / 2);
    }
}

.ksp__item__pre-figure {
    color: $color-alizarin;
    text-transform: uppercase;
    margin: 0;
    font-weight: $font-weight-medium;
    position: absolute;
    top: $measure-small;
    left: 0;
    width: 100%;
}

.ksp__item__text {
    padding-top: 10px;
    color: $color-lucifer;
}

.ksp__item__icon{
    display: inline-flex;
    border-radius: 100%;
    background-color: $color-alizarin;
     img{
        width: $measure-xenormous;
    }
}

$level-colors: (
    undergraduate:($color-ug-accent, invert(100%)),
    masters: ($color-msc-lead, invert(0%)),
    mba: ($color-mba-lead, invert(0%)),
    online-msc: ($color-msc-lead, invert(0%)),
    emba: ($color-mba-lead, invert(0%)),
    demba: ($color-mba-lead, invert(0%)),
    centres: ($color-centres-low, invert(0%)),
    corporate: ($color-corporate-red, invert(0%)),
    phd: ($color-phd-lead, invert(0%)),
    executive-phd: ($color-phd-lead, invert(0%)),
    executive-education: ($color-exec-lead, invert(0%))
);

@each $level, $colors in $level-colors {
    $bgColor: nth($colors, 1);
    $invert: nth($colors, 2);

    .ksp__item--#{$level} {
        .ksp__item__figure,
        .ksp__item__pre-figure {
            color: $bgColor;
        }

        .ksp__item__icon{
            background-color:  $bgColor;

            img{
                filter: $invert;
            }
        }
    }

    .scp--#{$level} {
        .ksp__item {
            .ksp__item__figure,
            .ksp__item__pre-figure {
                color:  $bgColor;
            }

            .ksp__item__icon{
                background-color:  $bgColor;

                img{
                    filter: $invert;
                }
            }
        }
    }
}
