.ksp-v23 {
    position: relative;
    padding: $measure--small4 0; // 2rem

    @include media('>between') {
        padding: $measure--small4; // 2rem
    }

    img {
        height: $measure--medium7; // 4rem;
        min-width: 60px;
    }

    @include media('<=between') {
        img {
            width: auto;
            margin-bottom: $measure--small2; // 1.5rem
        }
    }

    p {
        margin: 0;
    }

    ul {
        list-style-type: disc;
    }
}

.ksp-v23-block {
    @include media('>between') {
        @include x-column-grid(6);
        display: grid;
    }
}

.ksp-v23-block.ksp-v23-block--single {
    @include media('>between') {
        @include x-column-grid(12);
    }
}

.ksp-v23-block--1 {
    // #1, in case of two blocks
    width: 100%;
    grid-column: 1 / 7;

    @include media('<=between') {
        ul {
            margin-bottom: $measure--half;
        }
    }
}

.ksp-v23-block--2 {
    // #2, in case of two blocks
    grid-column: 7 / -1;

    .ksp-v23-img {
        @include media('<=between') {
            margin-top: $measure--small4; // 2rem
        }
    }
}

.ksp-v23-block--single {
    // in case of only one block
    grid-column: 1 / -1;
}

.ksp-v23-block-text {
    grid-column: 1 / -1;

    h3,
    h4 {
        margin: 0;
    }

    sup {
        @include extrasmalltype();
    }
}

.ksp-v23-block--img {
    .ksp-v23-block-text {
        grid-column: 2/ -1;
    }
}

.ksp-v23-img {
    grid-column: 1 / 2;
}

.ksp-v23-wrapper {
    background-color: $color-lavender-grey;
    color: $color-black;
    padding: $measure--basic;
    margin-bottom: $measure--small4; // 2rem

    a {
        color: $color-black;
        border-bottom-color: $color-black;

        &:hover {
            color: $color-white;
            background-color: $color-black;
        }

        &:active,
        &:focus {
            outline-offset: $border-width-thick-w20;
            outline-color: $color-black;
        }
    }
}

.ksp-v23-wrapper--dark {
    background-color: $color-lead-mid-grey;
    color: $color-black;
}

.ksp-v23-block-wrapper {
    @include media('>between') {
        @include x-column-grid(12);
        display: grid;
    }
}

.ksp-v23-block-text {
    max-width: $max-line-length;
}

.corner-border-v23 {
    @include corner-border-v23($color-corporate-accent-2);
}

@each $level, $color in $courses-v23-palette {
    $bgLight: nth($color, 6);
    $textColorLight: nth($color, 7);
    $bgDark: nth($color, 8);
    $textColorDark: nth($color, 9);
    $decoration: nth($color, 5);
    $decorationTickDLightBg: nth($color, 22);
    $link-bg: nth($color, 20);

    $lead-text: nth($color, 2);
    $anchor-highlight: nth($color, 16);

    .#{$level}-courses-v23 {
        .ksp-v23-wrapper--dark {
            background-color: $bgDark;
            color: $textColorDark;

            a {
                color: $lead-text;
                border-bottom-color: $lead-text;
                background-color: $link-bg;

                &:hover {
                    color: $anchor-highlight;
                    background-color: $lead-text;
                }

                &:active,
                &:focus {
                    outline-offset: $border-width-thick-w20;
                    outline-color: $lead-text;
                }
            }
        }

        .ksp-v23-wrapper--light {
            background-color: $bgLight;
            color: $textColorLight;

            .ksp-ordered-list--tick  {
                ul {
                    li:before {
                        color: $decorationTickDLightBg;
                    }
                }
            }
        }

        .ksp-ordered-list--tick {
            ul {
                li:before {
                    color: $decoration;
                }
            }
        }

        .corner-border-v23 {
            @include corner-border-v23($decoration);
        }

        .ksp-ordered-list--tick ul {
            @include custom-unordered-list('\f00c', $decoration);
        }
    }
}
