$landing-cta-colors: (
    masters: (
        cta--blue-powerup,
        cta--prussian
    ),
    undergraduate: (
        cta--orange,
        cta--yellow
    ),
    mba: (
        cta--mba-lead,
        cta--mba-accent
    ),
    phd: (
        cta--summer-sea,
        cta--allports
    ),
    executive-phd: (
        cta--summer-sea,
        cta--allports
    ),
    executive-education: (
        cta--purple,
        cta--pink
    ),
    online-msc: (
        cta--blue-powerup,
        cta--prussian
    ),
);

$banner-bracket-colors: (
    corporate: (
        $color-corporate-red
    ),
    postgraduate: (
        $color-msc-accent
    ),
    masters: (
        $color-msc-accent
    ),
    online-msc: (
        $color-msc-accent
    ),
    undergraduate: (
        $color-ug-accent
    ),
    phd: (
        $color-phd-accent
    ),
    executive-phd: (
        $color-phd-accent
    ),
    executive-education: (
        $color-exec-accent
    ),
    execed: (
        $color-exec-accent
    ),
    mba: (
        $color-mba-accent
    ),
    demba: (
        $color-mba-accent
    ),
    emba: (
        $color-mba-accent
    ),
    centres: (
        $color-centres-bright
    )
);
@mixin corner-banner-v24($color, $width: 1.5rem, $BGsize: 3rem) {
    height: 100%;
    background:
        linear-gradient(to right, $color $width, transparent $width) 0 0, //top left
        linear-gradient(to bottom, $color $width, transparent $width) 0 0, //top left
        linear-gradient(to right, $color $width, transparent $width) 0% 100%, //bottom-left
        linear-gradient(to top, $color $width, transparent $width) 0% 100%; //bottom-left

    background-repeat: no-repeat;
    background-size: $BGsize $BGsize;
}

@each $level, $color in $landing-cta-colors {
    $anchor-style-1: nth($color, 1);
    $anchor-style-2: nth($color, 2);

    .landing--#{$level} {
        .cta-group {
            .cta {
                @include anchor(#{$anchor-style-2});
            }

            .cta:nth-child(3) {
                @include anchor($anchor-style-1);
            }
        }
    }
}

@each $level, $color in $banner-bracket-colors {
    $color: nth($color, 1);

    .banner-vi__content {
        position: relative;
    }

    .banner-vi--theme--#{$level} {
        .banner-vi__content__corner {
            @include corner-banner-v24($color);

        }
    }

    .banner-vi.banner-vi--theme--#{$level} {

        .banner-vi__shape--left svg path,
        .banner-vi__shape__mobile--left svg path {
            fill: $color;
        }
    }
}
