/// grid from Web2020

@mixin default-margin() {
    margin-left: $measure--basic;
    margin-right: $measure--basic;
    max-width: $grid-width-extra-large;

    @include media('>between') {
        margin-left: $measure--small2;
        margin-right: $measure--small2;
    }
}

@mixin default-padding() {
    padding-left: $measure--small2;
    padding-right: $measure--small2;

    @include media('>between') {
        padding-left: $measure--small4;
        padding-right: $measure--small4;
    }
}

@mixin standard-box() {
    @include default-margin();
    @include default-padding();

    @include media('>large') {
        margin-left: auto;
        margin-right: auto;
    }
}

@mixin x-column-grid($columns: 24) {
    grid-template-columns: repeat($columns, 1fr);
    grid-column-gap: 0;

    @include media('>=between') { // 650px
        grid-column-gap: $measure--basic;
    }

    @include media('>medium') {  // 900px
        grid-column-gap: $measure--small2;
    }
}

@mixin standard-grid-columns-only() {
    @include x-column-grid(24);
}

@mixin standard-grid() {
    @include x-column-grid(24);
    display: grid;
}
