// General styling
.courses-v23 {
    .courses-v23__student-life {

        .student-life-v23__links {
            @include media('>medium'){
                @include x-column-grid(18);
                display: grid;
            }
        }

        .student-life-v23__links__cards {
            grid-column: 1/12;
        }

        .student-life-v23__links__unibuddy {
            grid-column: 12/-1;
        }
    }
}

// MBA specific override
.courses-v23.mba-courses-v23 {
    .courses-v23__student-life {
        .student-life-v23__links__cards {
            grid-column: 1/12;
        }

        .student-life-v23__links__unibuddy {
            grid-column: 12/-1;
        }
    }
}