@keyframes block-link-heading {
    from {background-size: 0% $border-width-thick;}
    to {background-size: 100% $border-width-thick;}
  }

  @keyframes block-link-heading2 {
    from {transform: translateX(-50%) scaleX(0);}
    to {transform: translateX(-50%) scaleX(1);}
  }

.block-links-v24{
    @include media('>medium') {
        display: grid;
        @include x-column-grid(18);
    }

    .block-links-v24__heading{
        grid-column: 6/-6;
        font-size: $font-size--theta;
        line-height: $line-height-ratio--heading;
        text-align: center;
        margin-bottom: 3rem;

        &:hover{
            .block-links-v24__heading__text-2{
                &:after{
                    animation-name: block-link-heading2; /* Fully expand on hover */
                    animation-duration: 1s;
                }
            }
        }
    }

    .block-links-v24__heading__text{

        width: fit-content;
        text-decoration: none;
        background-image: linear-gradient(#D61826, #D61826);
        background-position: 37% 100%; //to place the image in the bottom left corner. 0% sets the horizontal position, and 100% sets the vertical position.
        background-repeat: no-repeat;
        background-size: 100% $border-width-thick;
    }

    .block-links-v24__heading__text--animate{
        border-bottom: transparent;
        animation-name: block-link-heading;
        animation-duration: 1s;
    }

    .block-links-v24__items{
        grid-column: 2/-2;
    }

    .block-links-v24__items{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-row-gap: 1.5rem;
        grid-column-gap: 1.5rem;

        @include media('>small', '<=medium') {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }

        @include media('<=small') {
            grid-template-columns: repeat(1, 1fr);
        }

        .block-links-v24__items__anchor{
            padding: 1rem;
            @include anchor-reset();
            border-bottom: $border-width-hairline $color-gray80 $border-style-default;

            .block-links-v24__items__anchor__header{
                margin-bottom: initial;
            }

            .block-links-v24__items__anchor__header__icon{
                margin-left: $measure--quarter;
            }

            &:hover,
            &:focus,
            &:active{
                .block-links-v24__items__anchor__header__icon{
                    transform: translateX(0.75rem);
                    transition: transform 0.2s ease-in;             
                }
            }

            &:focus{
                outline: $border-width-outline $border-style-default $color-black;
                outline-offset: $border-width-thick;
                background-color: $color-black;
                color: $color-white;
                border-bottom-color: $color-black;
            }

            &:active{
                outline: $border-width-outline $border-style-alternate $color-black;
                outline-offset: $border-width-thick;
                background-color: transparent;
                color: $color-black;
            }
        }
    }

    .block-links-v24__items__anchor__text{
        margin: initial;
    }

    .block-links-v24__items__anchor__text--hide{
        @include media('<medium'){
            display: none;
        }
    }
}