/*
This minitype mixin will set the base font size and then increase it for anything less than the breakpoint "between".
*/
@mixin minitype() {
    font-size: $font-size--basic;
    line-height: $line-height-ratio--body;
    letter-spacing: #{dynamic-tracking($font-size--basic, $font-size--mobile)}em;

    @include media('>between') {
        font-size: $font-size--mini;
        letter-spacing: #{dynamic-tracking($font-size--mini, $font-size--mobile)}em;
    }

    @include media('>=large', '<1080p') {
        letter-spacing: #{dynamic-tracking(
                $font-size--mini,
                $font-size--desktop
            )}em;
    }

    @include media('>=1080p', '<4K') {
        letter-spacing: #{dynamic-tracking($font-size--mini, $font-size--1080p)}em;
    }

    @include media('>=4K', '<8K') {
        letter-spacing: #{dynamic-tracking($font-size--mini, $font-size--4K)}em;
    }

    @include media('>=8K') {
        letter-spacing: #{dynamic-tracking($font-size--mini, $font-size--8K)}em;
    }
}
.mini-type {
    @include minitype();
}

