.courses-v23__modules-accordion {

    .accordion-v23__heading__text {
        padding-right:  $measure--small4;
    }
}

.js {
    .courses-v23__modules-accordion {
        a {
            color: $color-white;
            border-bottom-color: $color-white;
            background-color: rgba(30, 30, 30, 0.5);

            &:hover,
            &:active,
            &:focus {
                color: $color-black;
                background-color: $color-white;
            }

            &:active,
            &:focus {
                outline-offset: $border-width-thick-w20;
                outline-color: $color-white;
            }
        }
        .accordion-v23__heading__label {
            margin-bottom: auto;
        }
        .accordion-v23__modules-heading {
            margin:$measure-medium 0 0 0;
            @extend %delta !optional;
            &:first-child {
                margin-top: 0;
            }
        }
        .accordion-v23 .accordion-v23__heading button {
            .accordion-v23__heading__indicator {
                align-items: flex-start;
                justify-content: flex-end;
                padding-right: 0.5rem;
                &::before {
                    position: static;
                    margin:0;
                }
            }
            
        }
    }
}