.courses-v23 {
    .courses-v23__key-info__wrap {
        display: flex;
        flex-direction: column;
        position: relative;
        margin: 0;
        padding: 0;
        width: 100vw;
        max-width: initial;

        @include media('>large') {
            padding-bottom: $measure--small4;
            padding-left: $measure--basic;
            padding-right: $measure--basic;
        }

        .courses-v23__key-info__image {
            position: absolute;
            z-index: z-index('root');
            width: 100%;
            height: 100%;
            overflow: hidden;
            left: 0;

            @include media('<between') {
                position: relative;
            }

            img {
                width: 100%;
                object-fit: cover;
            }
        }

        .courses-v23__key-info {
            @include full-screen;
            z-index: z-index('default');
            @include x-column-grid(18);
            display: grid;
            margin-top: $measure--huge5 * 2;
            position: relative;
            padding: $measure--small3 $measure--basic;

            @include media('<extralarge') {
                margin-top: $measure--huge5 * 1.7;
            }

            @include media('<large') {
                margin-bottom: 0;
                margin-top: $measure--huge5 * 2;
            }

            @include media('<between') {
                margin-top: -$measure--small4;
            }

            @include media('>=medium') {
                .courses-v23__key-info__details{
                    grid-column: span 6;
                }
    
                .courses-v23__key-info__ctas__wrap{
                    grid-column: span 5;
                }
    
                .courses-v23__key-info__standfirst__wrap{
                    grid-column: span 7;
                    
                }
                .courses-v23__key-info__clearing ~ .courses-v23__key-info__standfirst__wrap {
                    grid-column: span 6;
                }

            }
            
            > div {
                grid-column: span 6;

                @include media('<medium') {
                    grid-column: span 9;
                    margin-bottom: $measure--small1;
                }

                @include media('<between') {
                    grid-column: span 18;
                    margin-bottom: $measure--medium1;

                    &:last-of-type {
                        margin-bottom: 0;
                    }
                }
            }

            .courses-v23__key-info__standfirst__wrap {
                position: relative;
                @include media('<medium') {
                    grid-column: 1/-1;
                }
            }
        }

        .courses-v23__key-info__ctas__wrap {
            margin-top: $measure--basic;

            @include media('<between') {
                margin-top: 0;
            }
        }

        .courses-v23__key-info__details__heading {
            @extend %gamma !optional;
        }

        .courses-v23__key-info__details__content {
            padding-left: $measure--basic;

            p {
                margin-bottom: $measure--quarter;
            }
        }

        .courses-v23__key-info__details__content__label {
            font-weight: $font-weight-medium;
        }

        .courses-v23__key-info__details__entry__wrap {
            font-weight: $font-weight-medium;
            display: flex;
            flex-direction: row;
            margin-top: $measure--basic;

            &:before {
                @include font-family(icon);
                content: '\f19d';
                margin-right: $measure--half;
            }

            ul {
                margin: 0;
                list-style: none;
            }
        }

        .courses-v23__key-info__details__entry__current {
            margin-bottom: 0;
        }

        .courses-v23__key-info__details__content p,
        .courses-v23__key-info__details__entry__current,
        .courses-v23__key-info__details__entry__years a {
            @include minitype();
        }

        .courses-v23__key-info__cta {
            margin-bottom: $measure--basic;

            &:last-child {
                @include media('<between') {
                    margin-bottom: 0;
                }
            }
        }

        .courses-v23__key-info__standfirst {
            padding: $measure--small2 $measure--small1;
            margin-top: $measure--basic;
            position: relative;
            height: initial;

            @include media('<between') {
                margin-top: 0;
            }
        }

        .courses-v23__key-info__standfirst__text {
            @include font-family(meta);
            @extend %beta !optional;
            margin-bottom: $measure--basic;
            line-height: $line-height-small;
            font-weight: $font-weight-default;
        }   
    }

    &.mba-courses-v23 {
        .courses-v23__key-info {
            @include media('>=medium') {
                .courses-v23__key-info__ctas__wrap {
                    grid-column: span 5;
                }
                .courses-v23__key-info__standfirst__wrap {
                    grid-column: span 7;
                }
            }
            
        }
    }
}

@each $level, $color in $courses-v23-palette {
    $lead-color: nth($color, 8);
    $lead-text: nth($color, 2);
    $highlight-color: nth($color, 12);
    $cta-main: nth($color, 13);
    $cta-apply: nth($color, 14);
    $anchor-highlight: nth($color, 16);
    $anchor-background: nth($color, 20);

    .#{$level}-courses-v23 {
        .courses-v23__key-info {
            background-color: $lead-color;
            color: $lead-text;
        }

        .courses-v23__key-info__details {
            a {
                color: $lead-text;
                border-bottom-color: $lead-text;
                background-color: $anchor-background;

                &:hover,
                &:active,
                &:focus {
                    color: $anchor-highlight;
                    background-color: $lead-text;
                }

                &:active,
                &:focus {
                    outline-offset: $border-width-thick-w20;
                    outline-color: $lead-text;
                }
            }
        }

        .courses-v23__key-info__details__content {
            border-left: 5px
                $border-style-default
                transparentize($highlight-color, 0.4);
        }

        .cta--v23--ki {
            @include anchor($cta-main);
        }

        .cta--v23--ki--apply {
            @include anchor($cta-apply);
        }
    }
}

.ug-courses-v23  .courses-v23__key-info__wrap {
    .cta--v23--ki {
        border: $border-width-default-w20 solid $color-black;  //  0.125rem  =  2px
    }

    .courses-v23__key-info__clearing__heading {
        @extend %gamma !optional;
        margin-top: $measure--half;
        margin-bottom: $measure--half;
    }

    .courses-v23__key-info__clearing {

        &:first-child {
            display: none;

            @include media('<medium') {
                display: block;
                grid-column: span 12;
            }

            @include media('<between') {
                grid-column: span 18;
            }
        }

        background-color: $color-flamingo;
        padding: $measure--basic;

        @include media('<medium') {
            display: none;
        }

        p {
            margin-bottom: $measure--threequarters;
        }
    }
    .courses-v23__key-info__clearing__cta {
        margin-bottom: $measure--half;
      
        &:first-child {
            .cta--v23--ki {
                @include anchor(cta--v23--black);
            }
        }

        .cta--v23--ki {
            @include anchor(cta--pg-v23--ki);
        }
    }

    .courses-v23__apply__asset {
        max-width: $max-line-length;
    }
}
