////
/// Variables
/// @group abstracts
////

/// @type Length
$base-font-size: 16px !default;

/// @type Length
$font-size--mini: 0.875rem !default;
$font-size--small-089R: 0.89rem !default;

/// @type Length
$font-size--mobile: 16px !default; //w20
/// @type Length
$font-size--desktop: 18px !default; //w20
/// @type Length
$font-size--1080p: 20px !default;
/// @type Length
$font-size--4K: 40px !default;
/// @type Length
$font-size--8K: 92px !default; //w20
/// @type Length
$font-size--basic: 1rem !default; //w20

/// @type Length
$grid-width-min: 320px !default;
/// @type Length
$width-mobile: 432px !default; //w20
/// @type Length
$grid-width-small: 500px !default;
/// @type Length
$grid-width-between: 650px !default;
/// @type Length
$grid-width-tablet: 768px !default; //w20
/// @type Length
$grid-width-medium: 900px !default;
/// @type Length
$grid-width-large: 1200px !default;
/// @type Length
$grid-width-extra-large: 1440px !default;
/// @type Length
$grid-width-extra-large-plus-sides: 1600px !default;
/// @type Length
$width--1080p: 1920px !default;
/// @type Length
$width--4K: 3840px !default;
/// @type Length
$width--8K: 7680px !default;

/// @type Map
/// @prop {String} key - Breakpoint name
/// @prop {Length} value - Breakpoint value
/// @require {Variable} $grid-width-small
/// @require {Variable} $grid-width-medium
/// @require {Variable} $grid-width-large
$breakpoints: (
    'min': $grid-width-min,
    'mobile': $width-mobile,
    'small': $grid-width-small,
    'between': $grid-width-between,
    'tablet': $grid-width-tablet,
    'medium': $grid-width-medium,
    'large': $grid-width-large,
    'extralarge': $grid-width-extra-large,
    'extralargePlusSides': $grid-width-extra-large-plus-sides,
    '1080p': $width--1080p,
    '4K': $width--4K,
    '8K': $width--8K,
) !default;

/// @type Number
$grid-columns: 12 !default;
/// @type Length
$grid-padding: 1rem !default;
/// @type Length
/// @require {Variable} $grid-padding
$grid-padding-half: ($grid-padding / 2) !default;
/// @type Length
$fixed-sidebar-width: 17.5rem !default;
// 21em
/// @type Length
$line-length-minimum: #{$base-font-size * 21} !default;
// 30em
/// @type Length
$line-length-maximum: #{$base-font-size * 30} !default;
/// @type Length
$line-length-responsive: calc(
    #{$line-length-minimum} + (
            #{strip-units($line-length-maximum)} - #{strip-units(
                    $line-length-minimum
                )}
        ) * ((100vw - #{$grid-width-small}) /
                (
                    #{strip-units($grid-width-large)} - #{strip-units(
                            $grid-width-small
                        )}
                ))
);

/// @type Length
$measure-small: 0.5rem !default;
/// @type Length
$measure-small-relative: 0.5em !default;
/// @type Length
$measure-smallish: 0.75rem !default;
/// @type Length
$measure-smallish-relative: 0.75rem !default;
/// @type Length
$measure-small-proportional: 2vmin !default;
/// @type Length
$measure-medium: 1rem !default;
/// @type Length
$measure-medium-relative: 1em !default;
/// @type Length
$measure-medium-proportional: 4vmin !default;
/// @type Length
$measure-large: 2rem !default;
/// @type Length
$measure-large-relative: 2em !default;
/// @type Length
$measure-large-proportional: 8vmin !default;
/// @type Length
$measure-xlarge: 3rem !default;
/// @type Length
$measure-enormous: 3.5rem !default;
/// @type Length
$measure-xenormous: 4rem !default;
/// @type Length
$footer-bg-layer-height: 9rem !default;
/// @type Length
$footer-promo-box-offset-top: 10rem !default;

/// @type Number
$opacity-tint: 0.1 !default;
/// @type Number
$opacity-faint: 0.15 !default;
/// @type Number
$opacity-fifth: 0.2 !default;
/// @type Number
$opacity-half: 0.5 !default;
/// @type Number
$opacity-shade: 0.85 !default;
/// @type Number
$opacity-full: 1 !default;
/// @type Map
/// @prop {String} key - Opacity name
/// @prop {Number} value - Opacity value
/// @see {Function} opacities
$opacities: (
    'tint': $opacity-tint,
    'faint': $opacity-faint,
    'fifth': $opacity-fifth,
    'half': $opacity-half,
    'shade': $opacity-shade,
    'full': $opacity-full,
) !default;

/// @type Length
$border-width-hairline: 0.06rem !default;
/// @type Length
$border-width-default: 0.12rem !default;
/// @type Length
$border-width-outline: 0.18rem !default;
/// @type Length
$border-width-thick: 0.24rem !default;

/// @type String
$border-style-default: solid !default;
/// @type String
$border-style-alternate: dotted !default;

/// @type Length
$border-radius-default: 0.25rem !default;
/// @type Length
$border-radius-thin: 0.125rem !default;
/// @type Map
/// @see {Mixin} border-radius
$border-radii: (
    'default': $border-radius-default,
    'thin': $border-radius-thin,
    'hairline': $border-width-hairline,
) !default;

/// @type String
$font-display: swap !default;

/// @type Number
$font-weight-thin: 100 !default;
/// @type Number
$font-weight-hairline: $font-weight-thin !default; //w20
/// @type Number
$font-weight-thin: 200 !default; //w20
/// @type Number
$font-weight-light: 300 !default;
/// @type Number
$font-weight-default: 400 !default;
/// @type Number
$font-weight-normal: $font-weight-default; //w20
/// @type Number
$font-weight-text: 400 !default; //w20
/// @type Number
$font-weight-book: 500 !default;
/// @type Number
$font-weight-medium: 600 !default;
/// @type Number
$font-weight-bold: 700 !default;
/// @type Number
$font-weight-black: 800 !default;
/// @type Number
$font-weight-extrablack: 900 !default;

/// @type Number
$font-size-ratio: 6/5;

.bold,
%bold {
    font-weight: $font-weight-bold;
}

// 16px
/// @type Length
/// @require {Variable} $base-font-size
$font-size-default: 1rem !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-default
$font-size-delta: ($font-size-ratio * $font-size-default) !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-delta
$font-size-gamma: ($font-size-ratio * $font-size-delta) !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-gamma
$font-size-beta: ($font-size-ratio * $font-size-gamma) !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-beta
$font-size-alpha: ($font-size-ratio * $font-size-beta) !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-beta
$font-size-kappa: ($font-size-ratio * $font-size-alpha) !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-beta
$font-size-omicron: ($font-size-ratio * $font-size-kappa) !default;
/// @type Length
/// @require {Variable} $font-size-ratio
/// @require {Variable} $font-size-default
$font-size-small: 14px; // TOO SMALL: ($font-size-default / $font-size-ratio) !default;
/// @type Length
$font-size-responsive: calc(16px + (24 - 16) * (100vw - 400px) / (800 - 400));
/// @type Length
$font-size-bayes-banner: 3.5rem !default;


/// @type List
$font-family-meta: 'ff-meta-web-pro', 'Helvetica Neue', Tahoma, Verdana, sans-serif !default;
/// @type List
$font-family-serif: 'ibm-plex-serif', Tahoma, Verdana, serif !default;
/// @type List
$font-family-sans: 'ibm-plex-sans', Tahoma, Verdana, sans-serif !default;
/// @type List
$font-family-default: $font-family-sans;
/// @type List
$font-family-plex-mono: 'ibm-plex-mono', monospace !default;
/// @type List
$font-family-mono: $font-family-plex-mono !default;
/// @type List
$font-family-head: $font-family-meta !default;
/// @type List
$font-family-sans: $font-family-plex-sans !default;
/// @type List
$font-family-body: $font-family-sans !default;
/// @type List
///
$font-family-icon: 'Font Awesome 6 Pro' !default;
/// @type Map
/// @prop {String} key - Font Family name
/// @prop {Number} value - Font Family value
/// @require {Variable} $font-family-default
/// @require {Variable} $font-family-meta
/// @require {Variable} $font-family-serif
/// @require {Variable} $font-family-icon
/// @see {Mixin} font-family
$font-families: (
    'default': $font-family-body,
    'meta': $font-family-meta,
    'serif': $font-family-serif,
    'heading': $font-family-head,
    'mono': $font-family-mono,
    'icon': $font-family-icon,
) !default;

// 11:8
/// @type Number
$line-height-default: 1.625 !default;
// 1:2  aug. fourth / dim. fifth
/// @type Number
$line-height-augmented-fourth: 1.414 !default;
/// @type Number
/// @require {Variable} $line-height-aug-fourth
$line-height-diminished-fifth: $line-height-augmented-fourth !default;
/// @type Number
$line-height-perfect-fifth: 1.5 !default;
/// @type Number
$line-height-small: 1.2 !default;

/// @type Length
$rotation-default: 10deg !default;

/// @type Length
/// @see {Mixin} transition
$transition-duration: 0.2s !default;
/// @type Length
/// @see {Mixin} transition
$transition-duration-long: 0.5s !default;
/// @type String
/// @see {Mixin} transition
$transition-function: ease !default;

// Our z-indexes, visualised: https://s.codepen.io/chrisburnell/debug/3ece711566bbe7aa442ac44d459f7dc4
/// @type Map
/// @prop {String} key - z-index name
/// @prop {Number} value - z-index value
/// @see {Function} z-index
$z-indexes: (
    'below': -1,
    'root': 0,
    'default': 1,
    'above': 2,
    'navigation': 3,
    'modal': 4,
    'dialog': 5,
    'notification': 6,
) !default;

/// @type Map
/// @prop {String} key - box-shadow name
/// @prop {Number} value - box-shadow value
/// @see {Function} box-shadow
$box-shadows: (
    'alpha': 0 12px 24px 0,
    'beta': 0 8px 16px 0,
    'gamma': 0 6px 12px 0,
    'delta': 0 4px 8px 0,
    'epsilon': 0 1px 2px 0,
    'flat': 0,
    'card': 0 0 calc(#{$measure-medium + $measure-small}) 3px,
) !default;

/// @type Map
/// @prop {Length} key - Percentage for sizing
/// @prop {Array} value - Aspect Ratio
$ratios: (
    62.5%: (
        16,
        10,
    ),
    56.25%: (
        16,
        9,
    ),
    75%: (
        4,
        3,
    ),
) !default;

/// @type List
/// @prop {String} Side
$fallouts: ('left', 'right', 'both') !default;

/// @type List
$animation-directions: in, out !default;

/// @type Map
$animation-cardinal-directions: (
    top: -100%,
    bottom: 100%,
    left: -100%,
    right: 100%,
) !default;

/// @type List
$animation-axes-directions: horizontal, vertical !default;

/// from web2020
///
/// @type Length
$border-width-hairline-w20: 0.0625rem !default; //1px
/// @type Length
$border-width-default-w20: 0.125rem !default; //2px
/// @type Length
$border-width-thick-w20: 0.1875rem !default; //3px

