@import 'courses-v23/fee';
@import 'courses-v23/pie-chart';
@import 'courses-v23/key-info';
@import 'courses-v23/overview';
@import 'courses-v23/student-life';
@import 'courses-v23/apply-promo-area';
@import 'courses-v23/hallway';
@import 'courses-v23/related-courses';
@import 'courses-v23/entry-requirements';
@import 'courses-v23/study-abroad';
@import 'courses-v23/contact';
@import 'courses-v23/careers';
@import 'courses-v23/teaching-staff';
@import 'courses-v23/modules-accordion';

.courses-v23 {
    section:not(.courses-v23__section--full-width),
    > div:not(.courses-v23__section--full-width) {
        @include full-screen;
    }

    .courses-v23__section--full-width {
        max-width: 100%;
        .courses-v23__section__inner {
            @include full-screen;
        }
    }

    p,
    li {
        @include content-style();
    }

    h2:not(.accordion-v23__heading),
    h3:not(.accordion-v23__heading),
    h4:not(.accordion-v23__heading),
    a:not(.accordion-v23__heading):not(.course-card-v23__anchor) {
        max-width: $max-line-length;
    }

    table {
        margin: 0;
    }

    .accordion-v23 {
        margin-top: $measure--small1;
        margin-bottom: $measure--small1;

        .cta--v23 {
            @include anchor(cta--v23--black);
        }
    }

    sub,
    sup {
        font-size: 0.65em;
    }

    .youtube-embed--for-content,
    figure {
        max-width: $max-line-length;
    }
}

.courses-v23__grid-18 {
    @include media('>medium') {
        display: grid;
        @include x-column-grid(18);
    }
}
.courses-v23__title {
    h1 {
        margin: $measure--basic 0 0 0;
    }
}

.courses-v23__structure {
    @include media('>medium') {
        @include x-column-grid(18);
        display: grid;

        h2,
        h3 {
            grid-column: 1/-1;
        }

        .courses-v23__structure__content {
            grid-column: 1/12;
        }

        .courses-v23__structure__image {
            grid-column: 12/-1;
        }

        .accordion-v23 {
            grid-column: 1/12;
        }
    }

    // forces correct font style for 'pop up' modal trigger for content inside course content section
    .courses-v23__structure__content div[data-popup-def-list-category] li {
        h5,
        h6 {
            font-weight: $font-weight-text;
            font-family: $font-family-default;
            font-size: $font-size-default;
        }
    }

    .cta--course-spec {
        @include anchor(cta--v23--black);
        
        @include media('<=medium') {
            margin-bottom: $measure--basic;
        }
    }
}

.courses-v23__study-abroad {
    @include media('>medium') {
        @include x-column-grid(18);
        display: grid;

        h2,
        h3 {
            grid-column: 1/-1;
        }

        .courses-v23__study-abroad__content,
        .courses-v23__study-abroad__heading {
            grid-column: 1/12;
        }

        .courses-v23__study-abroad__image {
            grid-column: 12/-1;
        }
    }
}

.courses-v23__placements {
    @include media('>medium') {
        @include x-column-grid(18);
        display: grid;

        h2,
        h3 {
            grid-column: 1/-1;
        }

        .courses-v23__placements__content {
            grid-column: 1/12;
        }

        .courses-v23__placements__image {
            grid-column: 12/-1;
        }
    }
}

.courses-v23__careers {
    @include media('>medium') {
        @include x-column-grid(18);
        display: grid;
    }

    .courses-v23__careers__heading {
        grid-column: 1/-1;
    }

    .courses-v23__careers__content,
    .accordion-v23 {
        grid-column: 1/12;
    }

    .courses-v23__careers__alumni {
        grid-column: 12/-1;

        .slider-v23__container {
            padding: $measure--small2;
            position: relative;
        }
    }
}

.courses-v23__side-image-mobile-hidden {
    @include media('<=medium') {
        display: none;
    }
}

@each $level, $color in $courses-v23-palette {
    $decoration: nth($color, 22);

    .#{$level}-courses-v23 {
        .courses-v23__list-style-tick {
            @include custom-unordered-list(
                fa-content($fa-var-check),
                $decoration
            );
        }
    }
}

.mba-courses-v23 {
    .courses-v23__structure {
        @include media('>medium') {
            .courses-v23__structure__content {
                grid-column: 1/12;
            }

            .courses-v23__structure__image {
                grid-column: 12/-1;
            }

            .accordion-v23 {
                grid-column: 1/12;
            }
        }
    }

    .courses-v23__study-abroad {
        @include media('>medium') {
            .courses-v23__study-abroad__content,
            .courses-v23__study-abroad__heading {
                grid-column: 1/12;
            }
    
            .courses-v23__study-abroad__image {
                grid-column: 12/-1;
            }
        }
    }

    .courses-v23__placements {
        @include media('>medium') {
            .courses-v23__placements__content {
                grid-column: 1/12;
            }
    
            .courses-v23__placements__image {
                grid-column: 12/-1;
            }
        }
    }

    .courses-v23__careers {
        .courses-v23__careers__content,
        .accordion-v23 {
            grid-column: 1/12;
        }
    
        .courses-v23__careers__alumni {
            grid-column: 12/-1;
        }
    }

    .standfirst {
        @include standfirst;
        margin-bottom: $measure--basic;
        text-align: left;
        font-weight: $font-weight-default;
    }
}
