.courses-v23__apply__wrapper{

    @include media('>medium'){
        position: relative;
        padding-left: $measure--small4;
        padding-right: $measure--small4;
        padding-bottom: $measure--medium3;
        padding-top: $measure--medium3;
    }

    @include media('<=medium'){
        padding-bottom: $measure--medium3;
    }
}

.courses-v23__apply {
    position: relative;
    margin-top: $measure--small4;

    
    @include bracket-block($color-white);

    .bracket-block__anglesB {
        &:before {
            width: $measure--huge5;
            border-width: $measure--medium11 $measure--medium11 0 0;
        }

        &:after {
            width: $measure--huge5;
            border-width: 0 $measure--medium11 $measure--medium11 0;
        }

        @include media('<=medium'){
            &:after,
            &:before{
                right: -$measure--medium11;
            }

            &:after{
                width: $measure--huge1;
                border-width: 0 $measure--medium3 $measure--medium3 0;
            }

            &:before{
                width: $measure--huge1;
                border-width: $measure--medium3 $measure--medium3 0 0;
            }
        }
    }

    .courses-v23__apply__promo-content {
        position: relative;
        margin-top: $measure--small4;
        margin-bottom: $measure--small4;
        padding-top: $measure--small4;
        padding-right: $measure--medium1;
 
        .bracket-block__anglesA {
            display: none; // remove once HTML is updated
        }

        @include media('>medium') {
            padding-top: $measure--medium3;
            padding-bottom: $measure--medium3;
            padding-right: $measure--huge5;

            .courses-v23__apply__promo-content__cta-group {
                display: flex;
                column-gap: $measure--basic;
                justify-content: flex-start;
            }
        }

        @include media('<=medium') {
            .courses-v23__apply__promo-content__cta:nth-child(n + 2) {
                margin-top: $measure--basic;
            }
        }
    }

    .courses-v23__apply__right-bracket {
        position: relative;

        svg {
            transform: rotate(180deg);
            position: absolute;
            height: 101%;
            width: auto;
            right: 0;
        }
    }
}

@each $level, $color in $courses-v23-palette {
    $lead-color: nth($color, 8);
    $lead-text: nth($color, 2);
    $accent-color: nth($color, 3);
    $anchor-highlight: nth($color, 16);
    $anchor-background: nth($color, 20);
    $text-inverted-color: nth($color, 21);


    .#{$level}-courses-v23 {
        .courses-v23__apply {
            color: $lead-text;

            @include media('<=medium'){
                background-color: $lead-color;
            }

            .courses-v23__apply__wrapper{
                background-color: $lead-color;
            }

            a {
                color: $lead-text;
                border-bottom-color: $lead-text;
                background-color: $anchor-background;

                &:hover,
                &:active,
                &:focus {
                    color: $anchor-highlight;
                    background-color: $lead-text;
                }

                &:active,
                &:focus {
                    outline-offset: $border-width-thick-w20;
                    outline-color: $lead-text;
                }
            }


            .courses-v23__apply__cta {
                @if ($level=='mba') {
                    @include anchor(cta--mba-v23--ki--apply);
                    margin-bottom: $measure--basic;
                } @else {
                    @include anchor(cta--outline-v23, $lead-text);

                    &:hover,
                    &:focus,
                    &:active {
                        color:  $text-inverted-color;
                    }
                } 
            }

            .courses-v23__apply__promo-content {
                @include bracket-block($accent-color);

                @include media('<=medium'){
                    .bracket-block__anglesA{
                        &:after,
                        &:before{
                            left: 0;
                        }
                    }
                }
            }

            @if ($level=='pg') {
                .courses-v23__apply__promo-content__cta {
                    width: 100%;
                    @include anchor(cta--pg-v23--ki--apply);
                }
            } @else {
                .courses-v23__apply__promo-content__cta {
                    width: 100%;
                    @include anchor(cta--black);
                    border: $border-width-hairline solid $color-black;

                    &:hover,
                    &:active {
                        border: lighten($color-black, 12%);
                    }

                    &:active,
                    &:focus{
                        outline-color: $color-black;
                    }
                }
            }
        }
    }
}

.mba-courses-v23 {
    .courses-v23__apply {
        @include media('<=medium') {
            padding-left: $measure--small4;
            padding-right: $measure--small4;
        }

        @include media('<=between') {
            padding-left: $measure--basic;
            padding-right: $measure--basic;
        }
    }

    .courses-v23__apply__wrapper {

        @include media('>=medium') {
            padding: $measure--medium9 $measure--medium7;
        }
    }

    .courses-v23__apply__corner-border-v23 {
        position: absolute;
        left: 0;
        right: $measure--medium9;
        top: $measure--medium9;
        bottom: $measure--medium9;
        background: 
        //top right
            linear-gradient(
                to left,
                $color-mba-accent-v23 $measure--small5,
                transparent $measure--small5
            )
            100% 0,
        //top right
            linear-gradient(
                to bottom,
                $color-mba-accent-v23 $measure--small5,
                transparent $measure--small5
            )
            100% 0,
        //bottom right
            linear-gradient(
                to left,
                $color-mba-accent-v23 $measure--small5,
                transparent $measure--small5
            )
            100% 100%,
        //bottom-right
            linear-gradient(
                to top,
                $color-mba-accent-v23 $measure--small5,
                transparent $measure--small5
            )
            100% 100%; //bottom-right
        background-repeat: no-repeat;
        background-size: $measure--medium9 $measure--medium9;

        @include media('<=large') {
            right: $measure--small5;
            top: $measure--small5;
            bottom: $measure--small5;
            background: 
            //top right
                linear-gradient(
                    to left,
                    $color-mba-accent-v23 $measure--small5/2,
                    transparent $measure--small5/2
                )
                100% 0,
            //top right
                linear-gradient(
                    to bottom,
                    $color-mba-accent-v23 $measure--small5/2,
                    transparent $measure--small5/2
                )
                100% 0,
            //bottom right
                linear-gradient(
                    to left,
                    $color-mba-accent-v23 $measure--small5/2,
                    transparent $measure--small5/2
                )
                100% 100%,
            //bottom-right
                linear-gradient(
                    to top,
                    $color-mba-accent-v23 $measure--small5/2,
                    transparent $measure--small5/2
                )100% 100%;
            background-size: $measure--small5 $measure--small5;
            background-repeat: no-repeat;
        }

        @include media('<=tablet') {
            background: none;
        }
    }

    .courses-v23__apply__wrapper {
        &:before {
            content: '';
            position: absolute;
            height: $measure--medium9;
            width: $measure--medium9;
            background-color: $color-white;
            top: 0;
            display: flex;
            z-index: z-index('default');
            right: 0;

            @include media('<=large') {
                height: $measure--small5;
                width: $measure--small5;
            }
        }

        &:after {
            content: '';
            position: absolute;
            height: $measure--medium9;
            width: $measure--medium9;
            background-color: $color-white;
            bottom: 0;
            display: flex;
            z-index: z-index('default');
            right: 0;

            @include media('<=large') {
                height: $measure--small5;
                width: $measure--small5;
            }
        }
    }

    .courses-v23__apply__promo {
        ul {
            margin-left: $measure--small4;
        }
    }

    .courses-v23__apply__content {
        h3 {
            margin-top: $measure--medium3;
        }
    }

    .courses-v23__apply__content__items {
       list-style: none; 
    }

    .courses-v23__apply__content__item {
        ul {
            margin-left: $measure--small4;
        }

        h4 {
            margin-top: $measure--small4;
        }

        h5 {
            margin-left: $measure--threequarters;
            background-color: $color-white;
            color: $color-black;
            padding-left: $measure--half;
            padding-right: $measure--half;
            display:inline-block;
            width: initial;

            &~p {
                margin-left: $measure--threequarters;
            }
        }
    }
}
