////
/// Cards
/// @group card
////

.card--course {
    @include media('>between') {
        .card--course__anchor, .card--course__container {
            display: grid;
            grid-template-columns: repeat(18, 1fr);
            grid-column-gap: calc(#{$measure-medium + $measure-small});
        }

        figure {
            grid-column: 1/-1;
            grid-row: 1;
        }

        .txt {
            grid-column: 8/-1;
            grid-row: 1;
            margin-top: 20%;
        }
    }

    // Stops strong element style being ignored inside an anchor. As whole card is an anchor, there is sometimes a need for text to be made bold
    // to aid user experience
    strong {
        font-weight: revert;
    }

    // Generic
    margin-bottom: $measure-large;

    .card--course__anchor, .card--course__container {
        color: inherit;
        border-bottom-width: 0;
        background-clip: padding-box;
        background-color: $color-white;
    }

    figure {
        margin: 0;
        display: block; // Stops margins creating additional top/bottom space around card
    }

    .txt {
        position: relative;
        top: 0;

        @include media('<=between') {
            margin: calc(-#{$measure-large} * 3) $measure-large 0 0;
        }
        p {
            margin-bottom: $measure-small;
        }
        .icon {
            margin-right: $measure-small;
        }
        .label {
            font-weight: $font-weight-bold;
        }
    }

    .txt__heading {
        display: flex;
        padding: calc(#{$measure-small} / 2);
        margin: 0;
        padding: calc(#{$measure-small} + (#{$measure-small} / 2));

        a {
            @include anchor-dark-background();
        }
    }

    .txt__heading__content {
        margin: auto;
        text-decoration: underline;
        flex: 1;
    }

    .txt__heading__icon {
        margin: auto auto auto $measure-medium;
    }

    .txt__body {
        position: relative;
        padding: $measure-medium;
        background: $color-white-smoke;
    }

    // Programme card: variation of course card - custom styles below
    &.card--course--programme {
        .txt__heading {
            a {
                border-bottom: none;
                color: $color-white;
                display: flex;
                width: 100%;

                &:hover {
                    background-color: $color-black;
                }
            }
        }

        .txt__body__delivered-by, .txt__body__delivered-by + p {
            display: inline;
        }
    }
}

@each $level, $color in $study-level-colors {
    $bgColor: nth($color, 1);
    $textColor: nth($color, 3);

    .scp--#{$level} {
        .card--course {
            .txt__heading {
                background: $bgColor;
                color: $textColor;
            }

            .summary {
                color: $bgColor;
            }
        }
    }
}
