.js .slider-v23__wrap {
    .slider-v23[data-style='dots'] {
        display: flex;
        overflow: hidden;
        margin: $measure--basic 0;
        position: relative;
        @include slider-properties();

        @include media('<between') {
            margin: $measure--basic;
        }

        @include media('>=between') {
            &[data-style='buttons'] {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
            }
        }
            
        .slide {
            width: 100%;
            list-style: none;
            padding: $border-width-thick;
            margin-top: 0;
            top: 0;
            transition: left 1s ease-in-out;

            &:focus {
                @include outline-properties($color-black);
                outline-offset: -#{$border-width-thick};
            }

            &:last-of-type {
                margin-bottom: 0;
            }

            &.card--research {
                padding: $measure--basic;
            }

            &[data-sliderposition='-1'] {
                left: -100%;
                position: absolute;
            }

            &[data-sliderposition='0'] {
                left: 0;
                position: relative;
            }

            &[data-sliderposition='1'] {
                left: 100%;
                position: absolute;
            }

            & [data-hidden='true'] {
                visibility: hidden;
            }
        }
    }
    
    .slider-v23__controls--dots {
        text-align: center;
        margin-bottom: $measure--basic;

        button {
            border-radius: 50%;
            width: $measure--small1;
            height: $measure--small1;
            margin-right: $measure--threequarters; 	
            border: $border-width-default $border-style-default $color-black;

            &:last-of-type {
                margin-right: 0;
            }

            &:hover,
            &:focus,
            &:active {
                outline-offset: $border-width-thick-w20;
            }

            &[aria-expanded='true'] {
                background-color: $color-black;
            }
        }
    }

    &[data-count='1'] + .slider-v23__controls--dots {
        display: none;
    }
}
