.banner-vi--video-embedded {
    .banner-vi__container {
        flex-direction: row;
    }

    .banner-vi__container__column {
        flex-basis: 50%;
        align-items: center;
        display: flex;

        @include media('<=between') {
            flex-basis: 100%;
            align-items: center;
            flex-direction: column;
        }
    }

    .banner-vi__content {
        left: 0;
    }

    .banner-vi__content__text {
        width: inherit;
    }

    .banner-vi__container__gradient {
        @include media('<=between') {
            height: $measure-large * 1.5;
            width: 100%;
        }
    }

    .banner-vi__video__wrap {
        margin-top: $measure-medium * 3;
        margin-bottom: $measure-medium;

        @include media('<=between') {
            margin-top: $measure-medium;
        }
    }

    .banner-vi__video {
        position: relative;

        figcaption p:not(:first-child) {
            @include media('<=between') {
                margin-bottom: 0;
            }
        }
    }

    iframe {
        padding-left: $measure-medium;
        padding-right: $measure-medium;
    }

    figure {
        display: inherit;
    }

    figcaption {
        display: inline-block;
        width: 100%;
        padding-top: $measure-small;
        color: $color-lucifer;
        font-style: normal;
        font-family: $font-family-default;
        font-weight: $font-weight-default;
        padding-left: $measure-medium;
        padding-right: $measure-medium;

        .figcaption__bulleted-item {
            text-transform: uppercase;

            &:before {
                content: '\25CF';
                padding-left: $measure-small;
                padding-right: $measure-small;
            }
        }

        p:not(:first-child) {
            margin-top: $measure-small;
        }

        span {
            white-space: initial;
        }
    }

    .bayes-youtube-embed-container {
        display: block;
        width: 100%;
        padding: 0;
    }

    .youtube-embed-container__close-btn {
        display: none;
    }

    .cookieconsent-optout-marketing__inner {
        border: $border-width-thick $border-style-default $color-white;
    }

    @include media('>=between', '<medium') {
        .cookieconsent-optout-marketing {
            p {
                margin-bottom: $measure-small;
            }
        }

        .cookieconsent-optout-marketing__inner {
            padding: $measure-small;
        }

        .cookieconsent-optout-marketing__inner__content i {
            padding: $measure-small;
            &:before {
                padding: $measure-small;
                font-size: 1rem;
            }
        }
    }
}
