.js .courses-v23__careers__alumni {
    .slider-v23__heading {
        padding: 0 $measure--small2;
        margin-bottom: 0;
        font-size: $font-size-beta;
    }

    .slider-v23__container {
        padding: $measure--small2;
    }

    .slider-v23__cards[data-style='dots'] {
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
        background-color: $color-white;
        margin: 0;

        .alumni-card-v23 {
            display: flex;
            flex-direction: column;
            padding: $measure--small4;
        }
    }

    .alumni-card-v23__content__image {
        display: flex;
        justify-content: center;
        flex-direction: column;

        img {
            width: $measure--large3;
            @include border-radius(50%);
            margin-left: auto;
            margin-right: auto;
        }
        &:after {
            content: '';
            display: block;
            height: $measure--quarter;
            margin-left: auto;
            margin-right: auto;
            margin-top: $measure--small2;
            margin-bottom: $measure--small2;
            width: $measure--medium7;
        }
    }

    .alumni-card-v23__content__details {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: $measure--basic;
    }

    .alumni-card-v23__content__details__column {
        font-weight: $font-weight-bold;
        @include font-family(meta);
        font-size: $font-size-delta;
    }
}

.alumni-card-v23__cta__link {
    @include anchor(cta--v23--black);
}

.alumni-card-v23__content__image:after {
    background: $color-black;
}
.courses-v23 {
    .courses-v23__careers__alumni {
        .slider-v23__container {
            background: linear-gradient(
                        to right,
                        $color-lucifer $measure--medium3,
                        transparent $measure--medium3
                    )
                    0% 100%,
                linear-gradient(
                        to top,
                        $color-lucifer $measure--medium3,
                        transparent $measure--medium3
                    )
                    0% 100%;
            background-repeat: repeat, repeat;
            background-size: auto, auto;
            background-repeat: no-repeat;
            background-size: $measure--medium3 $measure--medium3;
        }
    }
}
