.courses-v23__careers__ksp {
    grid-column: 1/13;
}

.ksp-figures-v23.ksp-figures-v23--graphs {
    .ksp-figures-v23__list__item__value {
        stroke: $color-alizarin;
    }
}

.ksp-figures-v23 {
    position: relative;
    grid-column: 1/13;
    margin-top: $measure--small2;
    margin-bottom: $measure--small2;

    .ksp-figures-v23__list {
        display: flex;
        flex-direction: row;
        gap: $measure--basic;
        flex-wrap: wrap;

        &[data-count='1'],
        &[data-count='2'],
        &[data-count='3'] {
            .ksp-figures-v23__list__item {
                flex-basis: 25%;

                @include media('<=between') {
                    flex-basis: 50%;
                }
            }
        }

        &[data-count='4'] {
            .ksp-figures-v23__list__item {
                flex-basis: 23%;

                @include media('<=between') {
                    flex-basis: 50%;
                }
            }
        }
    }

    .ksp-figures-v23__list__item__label {
        p {
            font-size: $font-size--small-089R;
            line-height: $line-height-augmented-fourth;
            letter-spacing: inherit;
        }
    }

    &.ksp-figures-v23--graphs {
        .ksp-figures-v23__list {
            &[data-count='1'],
            &[data-count='2'],
            &[data-count='3'],
            &[data-count='4'] {
                .ksp-figures-v23__list__item {
                    @include media('<=between') {
                        flex-basis: 48%;
                    }
                }
            }
        }

        .ksp-figures-v23__list__item__value {
            position: relative;
            width: 100%;

            path {
                @extend %animate--svg-path-target;
            }
        }

        .ksp-figures-v23__list__item__value__number {
            position: absolute;
            display: flex;
            top: 0;
            width: 100%;
            height: 100%;
            justify-content: center;
            align-items: center;
            font-size: $font-size-bayes-banner;
            color: $color-black;
            @include font-family(meta);
        }

        .ksp-figures-v23__list__item__value__number__unit {
            font-size: $font-size-alpha;
        }

        .ksp-figures-v23__list__item__label {
            color: $color-emperor;
            text-align: center;
            margin-top: $measure--half;
        }
    }

    &.ksp-figures-v23--numbers {
        padding: $measure--small2;

        @include media('<=medium') {
            padding: $measure--small2;
        }

        .ksp-figures-v23__heading {
            margin-top: $measure--basic;
        }

        .ksp-figures-v23__list__item {
            position: relative;
            display: flex;
            flex-direction: row;
        }

        .ksp-figures-v23__list__item:nth-of-type(2) {
            @include media('<=between') {
                &:after {
                    content: inherit;
                }
            }
        }

        .ksp-figures-v23__list__item__container {
            flex: 1;
        }

        .ksp-figures-v23__list__item__value {
            display: flex;
            flex-direction: row;
            align-items: center;

            .ksp-figures-v23__list__item__value__number {
                font-size: $font-size-bayes-banner;
                color: $color-black;
                @include font-family(meta);
                
            }

            .ksp-figures-v23__list__item__value__unit {
                font-size: $font-size-alpha;
            }
        }
    }
}

// pattern variation for widget 2 x 2 grid
.mba-courses-v23 .courses-v23__careers__ksp__widget,
.pg-courses-v23  .courses-v23__careers__ksp__widget{  
    .ksp-figures-v23--graphs {
        display: none;
    }

    .ksp-figures-v23--numbers {
        padding: 0;
        margin:0;

        .corner-border-v23 {
            background: none;
        }

        .ksp-figures-v23__heading {
            margin-top: 0;
        }

        .ksp-figures-v23__list .ksp-figures-v23__list__item {
            flex-basis: calc(50% - #{$measure-small});

            @include media('>=between') {
                flex-basis: calc(45% - #{$measure-small});;
            }

            @include media('>=medium') {
                flex-basis: calc(50% - #{$measure-small});
            }
    
            &:nth-child(2):after {
                background: none;
            }
        }
    }
}

// MBA specific override
.mba-courses-v23 {
    .courses-v23__careers__ksp {
        grid-column: 1/12;
    }
}

//PG specific override gor kps
.pg-courses-v23  .courses-v23__careers__ksp__widget{
    padding-bottom: $measure--small4;
    .ksp-figures-v23--numbers {
        .ksp-figures-v23__list{
            .ksp-figures-v23__list__item {
                flex-basis: initial;
                &:nth-child(even){
                    width: 45%;
                    &:after {
                        content: initial;
                        background: none;
                        width: initial;
                        margin-right: initial;
                    }
                }

                &:nth-child(odd){
                    width: 55%;
                }

                @include media('<=small') {
                    &:nth-child(odd),
                    &:nth-child(even){
                        width: 100%;
                        &:after {
                            content: initial;
                            background: none;
                            width: initial;
                            margin-right: initial;
                        }
                    }
                }
                
            }
        }
    }
}


@each $level, $color in $courses-v23-palette {
    $lead-color: nth($color, 1);
    $accent-color: nth($color, 3);

    .#{$level}-courses-v23 {
        .ksp-figures-v23.ksp-figures-v23--graphs {
            .ksp-figures-v23__list__item__value__number {
                color: $lead-color;;
            }
            .ksp-figures-v23__list__item__value {
                stroke:$accent-color;
            }
        }

        
    }
}