.courses-v23--fee {
    @include media('>medium') {
        @include x-column-grid(18);
        display: grid;
    }

    .cta {
        display: inline-block;
    }

    .accordion-v23,
    .courses-v23__fee,
    .courses-v23--fee__wys {
        grid-column: 1/12;
    }

    h2,
    h3 {
        grid-column: 1/-1;
    }
}

.courses-v23__fee {
    @include x-column-grid(12);
    display: grid;
    margin-bottom: $measure--small2;

    @include media('<=between') {
        @include x-column-grid(6);
    }

    p,
    h2 {
        margin-bottom: 0;
        padding: 0;
    }

    p {
        margin-bottom: $measure--threequarters;
    }

    .courses-v23--fee__heading {
        font-weight: $font-weight-medium;
        margin-bottom: $measure--basic; // 1rem

        @include media('<=between') {
            margin-bottom: $measure--half; // 0.5rem
        }
    }
}

.courses-v23__fee-block {
    grid-column: span 6;

    & + .fee-entry {
        margin-top: $measure--small4;
    }
    & + & {
        @include media('<=between') {
            margin-top: $measure--small4;
        }
    }

    &:first-of-type {
        .courses-v23--fee__heading {
            @include media('<=between') {
                margin-top: 0;
            }
        }
    }

    .fee-tc-text {
        margin-top: $measure--threequarters;
    }
}

.courses-v23__fee__inner {
    padding: $measure--small4 $measure--small2; // 3.5rem 1.5rem

    & + & {
        margin-top: $measure--basic;
        @include media('>medium') {
            margin-top: $measure--small2;
        }
    }

    .fee-studymode {
        @include font-family('meta');
        @extend %delta !optional;
        font-weight: $font-weight-text;
        margin-bottom: 0;
    }

    .fee-amount {
        @extend %heading-elements !optional;
        @include font-family(meta);
        @extend %alpha !optional;
        font-weight: $font-weight-extrablack;
        &:not(:last-child) {
            margin-bottom: 0;
        }
    }
    .courses-v23__fee-studymodes-list {
        li:nth-child(n + 2) {
            padding-top: $measure--threequarters;
        }
        li:last-child {
            .fee-amount {
                margin-bottom: 0;
            }
        }
    }
    .fee-seperator {
        padding-top: $measure--half;
    }

    .fee-currency-converter {
        padding: $measure--threequarters 0 0 0;
        margin: 0;
        font-weight: $font-weight-bold;
        font-size: $font-size-default;

        a {
            @include font-family(meta);
            color: $color-white;
            border-bottom-color: $color-white;
            background-color: rgba(30, 30, 30, 0.5);

            &:hover {
                color: $color-black;
                background-color: $color-white;
            }

            &:focus {
                outline-offset: $border-width-thick-w20;
                outline-color: $color-white;
                background-color: $color-white;
                color: $color-black;
            }

            &:active {
                outline-offset: $border-width-thick-w20;
                outline-color: $color-white;
            }
        }
    }

    .fee-amount__sub {
        font-weight: $font-weight-book;
        @extend %gamma !optional;
    }
}

@each $level, $color in $courses-v23-palette {
    $bgDark: nth($color, 8);
    $textColorDark: nth($color, 9);
    $borderColor: nth($color, 11);
    $highlight-color: nth($color, 12);

    .#{$level}-courses-v23 {
        .courses-v23__fee__inner {
            background-color: $bgDark;
            color: $textColorDark;

            .fee-seperator {
                border-top: $border-width-hairline-w20 solid $borderColor;
            }
        }
        .courses-v23__fee-studymodes-list {
            li {
                border-bottom: transparentize($highlight-color, 0.75) $border-width-hairline-w20 solid;
            }
        }
    }
}

@each $level, $color in $courses-v23-palette {
    $cta: nth($color, 10);

    .#{$level}-courses-v23 {
        .courses-v23--fee {
            .cta {
                @include anchor($cta);
            }
        }
    }
}

// MBA specific override
.mba-courses-v23 {
    .courses-v23--fee {
        .accordion-v23,
        .courses-v23__fee,
        .courses-v23--fee__wys {
            grid-column: 1/12;
        }
    }
}
