.student-life-v23__links{
    .student-life-v23__links__cards{
        list-style: none;
        margin-left: initial;
        margin-bottom: initial;

        @include media('>=between'){
            display: grid;
            grid-template-columns: repeat(2,1fr);
            grid-column-gap: $measure--small2;
            grid-row-gap: $measure--small2;
        }

        li{
            margin-top: initial;

            @include media('<=medium'){
                margin-bottom: $measure--basic;
                max-width: initial;
            }

            a{
                @include anchor(reset);
                @include anchor-block();
                outline-offset: $measure--quarter;

                @include media('<=medium'){
                    max-width: initial;
                }
            }
        }
    }

    .student-life-v23__links__card{
        position: relative;
    }

    .student-life-v23__links__card--image{
        &::after{
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            width: 100%;
            background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.5), transparent);
        }
    }

    .student-life-v23__links__card--label{
        position: absolute;
        background-color: $color-white;
        color: $color-black;
        bottom: $measure--small1;
        left: $measure--small1;

        .student-life-v23__links__card--label__heading{
            display: inline-flex;
            margin-bottom:initial;
            text-wrap: wrap;
        }

        .student-life-v23__links__card--label__heading-text{
            padding: $measure--half;
        }

        .student-life-v23__links__card--label__heading-icon{
            color: $color-white;
            font-size: $font-size-default;
            padding: $measure--half $measure--basic;
            background-color: $color-ug-accent;
            line-height: 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }
}

@each $level, $color in $courses-v23-palette {
    $color-accent: nth($color, 3);
    $color-text-accent: nth($color, 4);

    .#{$level}-courses-v23 {
        .student-life-v23__links {
            .student-life-v23__links__card--label{
                .student-life-v23__links__card--label__heading-icon{
                    background-color: $color-accent;
                    color: $color-text-accent;
                }
            }
        }
    }
}