
.banner-new{
    .banner__over-image:not(.banner__over-image--no-img-bg) {
        @include media('>between'){
            position: absolute;
            top: 0;
            width: 100%;
        }
    }
}


.banner-new.banner--image {
    .banner__container,
    .banner__content {
        height: 100%;
    }

    .banner__content__text {
        @include media('>medium'){
            margin-bottom: 3 * $measure-large;
        }
        @include media('>between','<=medium'){
            margin-bottom: $measure-large;
        }
    }

    .banner__content__text__standfirst {
        color: $color-white;
    }

    .banner__content__ctas {
        text-align: center;

        a {
            @include anchor(draw-outline, $color-white);
            margin: 0 $measure-medium $measure-large;
        }
    }

    &.banner--image--with-promo .banner__content__ctas {
        @include media('>medium') {
            text-align: left;
        }
        @include media('>large') {
            text-align: center;
        }
    }

    @include media('<=medium') {
        margin-bottom: $measure-large;
    }

    @include media('>medium') {
        &.banner--cutout--left,
        &.banner--cutout--right {
            .banner__content {
                margin-bottom: $cutout-height;
            }
            .banner__content__ctas {
                bottom: 3rem;
            }

            .banner__standfirst {
                margin-top: -$cutout-height / 2;
                position: relative;
                z-index: z-index(above);
            }
        }
    }
}

.banner-new{
    .banner__img {
        height: 100%;

        > picture{
            position: relative;
        }
    
        > picture > img{
            max-width: initial;
            min-width: 100%;
        }
    
        .banner--full-overlay &,
        .banner--partial-overlay & {
            &:after {
                display: block;
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: black;
                opacity: 0.8;
            }
        }
    
        .banner--partial-overlay & {
            &:after {
                background: linear-gradient(0deg, black, 70%, transparent) no-repeat
                    bottom;
                background-size: 100% 70%;
            }
        }
    }
}

.banner-new.banner--image{
    .banner__img{

        > picture > img {
            margin-left: 50%;
            transform: translateX(-50%);
        }

        @include media('>between'){
            max-height: 520px;
        }
    }
}

.banner-news.banner-new{
    .banner__img{
        img{
            position: relative;
        }
    }
}