////
/// Sticky Mobile
/// @group components
////

.nav-sticky__wrap {
    position: relative;
    &.nav-sticky__inner--stick {
        position: fixed;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0%);
        z-index: z-index(above);

        @include media('<small') {
            padding-left: $measure--half;
            padding-right: $measure--half;
        }

        @include media('>extralarge') {
            max-width: $grid-width-extra-large;
        }
    
        @include media('>1080p') {
            max-width: 83.333%;
        }
    }
}

.nav-sticky__helper {
    width: 100%;
    height: 0px;
    visibility: hidden;
}

.nav-sticky__inner {
    background: $color-white;
    z-index: z-index(above);
    max-width: 100%;
    width: 100%;
    
    &:before {
        content: '';
        background-color: $color-white;
        width: 100vw;
        position: absolute;
        height: 100%;
        left: calc( -1 * ((100vw - 100%) / 2));
        box-shadow: 0 0.18rem 0.24rem rgba(0,0,0,0.2);
    }
}

.nav-sticky {
    @include font-family(meta);

    &.nav-sticky--left:before {
        content: '';
        width: $measure--medium7;
        height: 100%;
        background: linear-gradient(
            to left,
            rgba(255, 255, 255, 0) 0%,
            rgba(255, 255, 255, 0.9) 60%
        );
        position: absolute;
        left: 0;
        top: 0;
        z-index: z-index(above);
        @include media('<medium') {
            width: $measure--medium3;
        }

        @include media('<small') {
            width: $measure--small2;
        }
    }

    &.nav-sticky--right:after {
        content: '';
        width: $measure--medium7;
        height: 100%;
        background: linear-gradient(
            to right,
            rgba(255, 255, 255, 0) 0%,
            rgba(255, 255, 255, 0.9) 60%
        );
        position: absolute;
        right: 0;
        top: 0;
        z-index: z-index(above);

        @include media('<medium') {
            width: $measure--medium3;
        }

        @include media('<small') {
            width: $measure--small2;
        }
    }

    .nav-sticky__items {
        display: flex;
        flex-direction: row;
        align-items: flex-start;
        list-style: none;
        overflow-x: auto;
        scroll-behavior: smooth;
        white-space: nowrap;
        gap: $measure--half;
        grid-column: 1/-1;
        padding-top: $measure--half;
        padding-bottom: $measure--half;
        position: relative;
        margin-left: -$measure--half;
        padding-left: $measure--half;
        margin-right: -$measure--half;
        padding-right: $measure--half;
    }

    .nav-sticky__item {
        margin-top: 0;
        display: inherit;

        &:focus-within {
            outline: $border-width-outline $border-style-default $color-black;
            outline-offset: $border-width-thick-w20;
            color: $color-white;
        }
    }

    .nav-sticky__item__link {
        padding: $measure--half;
        background: $color-titanium-white;
        @include border-radius(3px);
        border-bottom: none;
        color: $color-woodsmoke;
        @include extrasmalltype();
        &:focus {
            outline: none;
        }

        &:hover {
            background: $color-gray80;
            text-decoration: underline;
        }

        &:active {
            outline: none;
        }
    }
}

.sticky-nav__sec {
    padding-top: $measure--medium3;
}

html {
    scroll-behavior: smooth;
}

@each $level, $color in $courses-v23-palette {
    $accentt-color: nth($color, 3);
    $accentt-text: nth($color, 4);

    .#{$level}-courses-v23 {
        .nav-sticky {
            .nav-sticky__item__link {
                &:focus,
                &:active {
                    background: $accentt-color;
                    color: $accentt-text;
                }
            }

            .nav-sticky__item__link__active {
                background: $accentt-color;
                color: $accentt-text;
            }
        }
    }
}
