

.banner-new{
    .animation-reveal {
        background-color: transparent;
        width: 100%;
        height: auto;
        position: relative;
    
        @include media('>between'){
            &:after {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                animation-duration: $animation-duration;
                animation-timing-function: ease;
                animation-direction: both;
            }
        
            &:after {
                background-color: white;
                animation-delay: ($animation-duration / 3);
            }
        
            .animation-reveal__content {
                opacity: 0;
            }
        }
    }
}

.banner-new,
.banner-no-img-varieties.banner-no-img{
    .animation-reveal__load--right-to-left,
    .animation-reveal__load--left-to-right {
        overflow: hidden;
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0);

        &:after {
            left: 0;
            right: 0;
            transform: translateX(100%);
        }

        .animation-reveal__content {
            animation: content-reveal 0s linear ($animation-duration/1.2) forwards;
        }

        &:after {
            animation-name: video-slide--left-to-right;
        }
        @include media('<=between'){
            box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.30);
        }
    }
}
