.courses-v23{
    .chart-row__items__chart{
        @include media('>small'){
            width: 33%;
        }
    }

    .chart-row__items__chart__row{        
        @include media('>small'){
            flex-direction: column;
            align-content: center;
        }
    }

    .chart-row__items__chart__row__legend{
        @include media('>small'){
            width: 90%;
        }
    }
    
    .chart-row__items__chart__row__legend__item{
        text-align: center;

        &:not(:last-child){
            &:after{
                content: initial;
                position: initial;
                bottom: initial;
                left: initial;
                background: initial;
                height: initial;
                width: initial;
            }
        }
    }

    .chart-row__items__chart__row__graph{
        padding-bottom: $measure--basic;

        @include media('>small'){
            width: 90%;
        }
    }

    //Workload accordion styles
    .workload-charts{
        .chart-row__items__chart{
            width: 100%;
        }

        .chart-row__items__chart__row{        
            @include media('>small'){
                flex-direction: row;
            }
        }

        .chart-row__items__chart__row__graph{
            width: 33%;
        }

        .chart-row__items__chart__row__legend{
            width: 66%;
        }

        .chart-row__items__chart__row__legend__item{
            text-align: left;
            padding-left: $measure--basic;
        }

        .chart-row__items__chart__row__legend__item__label{
            margin-left: initial;
        }
    }
}

@each $level, $color in $courses-v23-palette {
    $firstColor: nth($color, 1);
    $secColor: nth($color, 3);
    $thirdColor: nth($color, 18);
    $fourthColor: nth($color, 19);

    .#{$level}-courses-v23 {
        .chart-row__items__chart{
            path{
                &:nth-child(1){
                    stroke: $firstColor;
                }

                &:nth-child(2){
                    stroke: $secColor;
                }

                &:nth-child(3){
                    stroke: $thirdColor;
                }

                &:nth-child(4){
                    stroke: $fourthColor;
                }
            }

            .chart-row__items__chart__row__legend__description ~ .chart-row__items__chart__row__legend__item{
                &:nth-child(2){
                    .fa.fa-circle{
                        color: $firstColor;
                    }
                }

                &:nth-child(3){
                    .fa.fa-circle{
                        color: $secColor;
                    }
                }

                &:nth-child(4){
                    .fa.fa-circle{
                        color: $thirdColor;
                    }
                }

                &:nth-child(5){
                    .fa.fa-circle{
                        color: $fourthColor;
                    }
                }
            }

            .chart-row__items__chart__row__legend__item{   
                &:nth-child(1){
                    .fa.fa-circle{
                        color: $firstColor;
                    }
                }

                &:nth-child(2){
                    .fa.fa-circle{
                        color: $secColor;
                    }
                }

                &:nth-child(3){
                    .fa.fa-circle{
                        color: $thirdColor;
                    }
                }

                &:nth-child(3){
                    .fa.fa-circle{
                        color: $fourthColor;
                    }
                }
            }
        }

        .workload-charts{
            .chart-row__items__chart__row__legend__description ~ .chart-row__items__chart__row__legend__item{
                &:nth-child(2){
                    border-left: $measure--half solid $firstColor;
                }

                &:nth-child(3){
                    border-left: $measure--half solid $secColor;
                }

                &:nth-child(4){
                    border-left: $measure--half solid $thirdColor;
                }

                &:nth-child(5){
                    border-left: $measure--half solid $fourthColor;
                }
            }
        }
    }
}
