/*
* Styles for arrow and responsive slider
* These are general styles to rendering the slider at a basic fucntional level. If you need to create a unique slider then please create overriding rules 
* in a seperate file to respresent your slider. i.e. the styles for the v23 course responsive and dot sliders are contained in its own files individually.
*/
@mixin slider-properties() {
    overflow: hidden;
    margin: $measure--basic 0;
    position: relative;

    .slide {
        top: 0;
        transition: left 1s ease-in-out;
        width: 100%;
    }
}

.js {
    .slider-v23[data-style='arrows'] {
        display: flex;
        &[data-small='true'] {
            @include media('<between') {
                @include slider-properties();
            }

            @include media('>=between') {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                margin: 0;
            }
        }

        &:not([data-small='true']) {
            @include media('<between') {
                @include slider-properties();
                margin: $measure--basic;
            }

            @include media('>=between') {
                @include slider-properties();

                &[data-style='buttons'] {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: nowrap;
                }
            }
        }

        .slide {
            width: 100%;
            list-style: none;
            padding: $border-width-thick;
            margin-top: 0;

            &:focus {
                @include outline-properties($color-black);

                outline-offset: -#{$border-width-thick};
            }

            &:last-of-type {
                margin-bottom: 0;
            }

            &.card--research {
                padding: $measure--basic;
            }
        }

        &[data-cardsperrow='1'] {
            &[data-small='true'] {
                @include media('<between') {
                    .slide {
                        &[data-sliderposition='-1'] {
                            left: -100%;
                            position: absolute;
                        }

                        &[data-sliderposition='0'] {
                            left: 0;
                            position: relative;
                        }

                        &[data-sliderposition='1'] {
                            left: 100%;
                            position: absolute;
                        }
                    }
                }
            }

            &:not([data-small='true']) {
                .slide {
                    &[data-sliderposition='-1'] {
                        left: -100%;
                        position: absolute;
                    }

                    &[data-sliderposition='0'] {
                        left: 0;
                        position: relative;
                    }

                    &[data-sliderposition='1'] {
                        left: 100%;
                        position: absolute;
                    }
                }
            }
        }
    }

    // General nav/controls styling for both arrow and responsive slider
    .slider-v23 {
        & + .slider-v23__controls {
            align-items: center;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: flex-end;
            margin-top: $measure--basic;
            margin-bottom: $measure--basic;
            padding: $border-width-thick;

            .slider-v23__indicator {
                .slider-v23__indicator__current,
                .slider-v23__indicator__divider {
                    color: $color-gray7;
                }

                .slider-v23__indicator__divider {
                    margin-left: $measure--quarter;
                    margin-right: $measure--quarter;

                    .slider-v23__indicator__divider--sr {
                        @include screenreader-only;
                    }
                }

                .slider-v23__indicator__total {
                    color: $color-alizarin;
                }
            }

            .slider-v23__controls__prev,
            .slider-v23__controls__next {
                background-color: transparent;
                border-color: $color-alizarin;
                border-style: $border-style-default;
                border-width: $border-width-hairline;
                color: $color-alizarin;
                padding: $measure--threequarters;
                transition: background-color $transition-duration ease-in,
                    border-color $transition-duration ease-in,
                    color $transition-duration ease-in;

                &:focus:not(:disabled),
                &:active:not(:disabled) {
                    @include outline-properties($color-black);
                }

                &:disabled {
                    border-color: transparent;
                    color: $color-gray7;
                    cursor: default;
                }
            }

            .slider-v23__controls__prev,
            .slider-v23__controls__next {
                font-weight: $font-weight-normal;

                span {
                    @include screenreader-only;
                }
            }

            .slider-v23__controls__prev {
                &:before {
                    content: unquote('"#{ $fa-var-chevron-left }"');
                    @include font-family(icon);
                    font-weight: 900;
                }
            }

            .slider-v23__controls__next {
                &:before {
                    content: unquote('"#{ $fa-var-chevron-right }"');
                    @include font-family(icon);
                    font-weight: 900;
                }
            }
        }

        &[data-small='true'] {
            & + .slider-v23__controls {
                @include media('>=tablet') {
                    display: none;
                }
            }
        }
    }

    .slider-v23 {
        & + .slider-v23__controls--arrows {
            max-width: 44.4rem;
            margin-right: 0;
            margin-left: $measure--basic;
        }
    }
}
