.banner-vi--listing {
    .banner-vi__content {
        padding-left: 0;
    }

    .banner-vi__content__text {
        @include media('>between') {
            padding-top: $measure-large * 2.5;
        }

        @include media('<=between') {
            margin-top: $measure-large * 1.5;
        }
    }

    &[data-animated='yes'].banner-vi--gradient,
    &[data-animated='yes'].banner-vi--gradient
        + main
        .container {
        @include media('>between') {
            background-size: 400% 400%;
            animation: gradient-wave ease-out 12s infinite;

            @keyframes gradient-wave {
                0% {
                    background-position: 0% 50%;
                }
                50% {
                    background-position: 100% 50%;
                }
                100% {
                    background-position: 0% 50%;
                }
            }
        }
    }

    & + main {
        .course-finder__top,
        .expert-finder__top {
            padding: 0;

            h2 {
                display: none;
            }
            .container {
                margin: 0;
                max-width: initial;
                position: relative;
                overflow: hidden;
                padding: 0;
            }

            .query-box {
                padding-top: $measure-large;
                padding-bottom: $measure-large;
                height: 100%;
                @include grid-top;
                @include media('<=between') {
                    display: block;
                    margin-left: initial;
                    margin-right: initial;
                    max-width: initial;
                }

                @include media('>between') {
                    padding-bottom: $measure-large * 2.5;
                }

                @include media('<=between') {
                    position: relative;
                    padding-top: $measure-medium;
                    padding-bottom: $measure-medium;
                }
            }

            input {
                position: relative;
                border: none;
                border-radius: $border-radius-default;

                @include media('>=extralargePlusSides') {
                    @include dynamicFontSize4k();
                }

                @include grid-column-map(
                    (
                        'min': 12,
                        'small': 12,
                        'between': 10,
                        'medium': 8,
                        'large': 5,
                        'extralarge': 5,
                    )
                );

                /**/
                @include grid-column-push-map(
                    (
                        'small': 0,
                        'extralarge': 1,
                    )
                );

                @include media('<=between') {
                    border-radius: $border-radius-default;
                    border: $border-width-default $border-style-default
                        $color-black;
                }

                &:focus-visible {
                    outline: $border-width-default $border-style-default
                        $color-black;
                }
            }

            button {
                position: relative;
                border: none;
                border-radius: 0 $border-radius-default $border-radius-default 0;
                left: calc(8.333% - 46px);

                @include media('<=extralarge') {
                    margin-right: $measure-small;
                    left: -46px;
                }

                @include media('<=between') {
                    position: absolute;
                    left: inherit;
                    background: none;
                    right: 0;
                    margin-top: $measure-medium;
                    margin-right: $measure-medium;
                    height: 49px;
                    width: 44px;
                }

                @include media('<=small') {
                    margin-right: $measure-small;
                }
            }

            .course-finder__clear-buttons,
            .expert-finder__clear-buttons {
                display: flex;
                align-items: baseline;
                margin-bottom: $measure-smallish;
                @include grid-top;
                @include media('<=between') {
                    display: block;
                    margin-left: initial;
                    margin-right: initial;
                    max-width: initial;
                }

                position: initial;

                @include media('<=between') {
                    margin-bottom: 0;
                    position: relative;
                }

                &:after {
                    position: absolute;
                    content: '';
                    width: 100%;
                    height: 100%;
                    background: $color-white-smoke;
                    left: 0;

                    @include media('<=between') {
                        top: 0;
                    }
                }

                div:first-child {
                    margin-top: $measure-large;

                    @include media('<=between') {
                        margin-top: $measure-medium;
                    }
                }

                a {
                    z-index: z-index(default);
                    background-color: $color-woodsmoke;

                    &:hover {
                        background-color: $color-black;
                    }
                }
            }
        }

        .course-finder__main,
        .expert-finder__main {
            .filter-box__open {
                border-radius: $border-radius-default;
                border: $border-width-default $border-style-default $color-black;
                background: none;
                color: $color-black;

                span {
                    &:before {
                        @include filter-box-down-arrow;
                        transition:translateY(-50%) transform 300ms;
                    }

                    &.fa-angle-up:before {
                        transform:translateY(-50%)  rotate(180deg);
                    }
                }
            }

            .filter-box__inner {
                background: none;

                h2 {
                    @include media('>between') {
                        display: block;
                    }
                }
            }
        }
    }
}
