.courses-v23__contact {
    background-color: $color-white-smoke;
    margin-top: $measure--small4; //2rem
    padding-bottom: $measure--small4; //2rem
    position: relative;
    z-index: z-index('root');

    .kis-widget {
        margin-top: $measure--small4;
    }

    &:after {
        content: '';
        position: absolute;
        height: 100%;
        width: 100vw;
        background-color: $color-white-smoke;
        top: 0;
        display: flex;
        z-index: z-index('below');
    }

    &:before {
        content: '';
        position: absolute;
        height: 100%;
        width: 50vw;
        background-color: $color-white-smoke;
        top: 0;
        display: flex;
        z-index: z-index('below');
        left: -50%;
    }

    .contact__heading {
        margin-bottom: 0;
    }

    .contact-highlight {
        @include h2style();
        @include text-highlight($color-white, $color-black);
        border: $measure--quarter solid $color-black;
    }
}

.courses-v23__contact__cta {
    @include media('>medium') {
        display: flex;
        justify-content: space-between;
    }

    li {
        margin-top: $measure--half;

        @include media('>medium') {
            display: inline-block;
            margin-top: 0;
            margin-right: $measure--half;
            flex-grow: 1;
        }
    }
}

.ug-courses-v23 {
    .courses-v23__contact__cta {
        a {
            @include anchor-cta--black();
            
            &:active,
            &:focus{
                outline-color: $color-black;
            }
        }
    }
}

.pg-courses-v23 {
    .courses-v23__contact__cta {
        a {
            @include anchor-cta--prussian();
        }
    }
}

.courses-v23 {
    .courses-v23__contact__cta {
        a {
            @include anchor(cta--v23--black);
        }
    }

    .courses-v23__contact__wrapper {
        display: flex;
        flex-direction: column;

        @include media('>medium') {
            flex-direction: row;
        }
    }
    .courses-v23__contact__container {
        @include media('>medium') {
            margin-top: $measure-large;
            margin-bottom: $measure-xlarge;
        }
    }

    .courses-v23__contact__container__text {
        flex-grow: 1;
    }

    .courses-v23__contact__container__cta {
        margin-left: $measure-large;
        flex-grow: 5;

        @include media('<=medium') {
            margin-top: $measure-large;
            margin-left: 0;
        }
    }
}
