.header__site-search {

    z-index: z-index(dialog);

    @include grid-column-base;
    position: static;

    text-align: right;
    padding-right: $grid-padding-half;

    @supports (display: flex) {
        align-self: center;
    }

    width: grid-column-width(1);

    @include media('<=between') {
        width: grid-column-width(2);
    }

    @include media('>large') {
        width: grid-column-width(2);
    }

    #search{
        @include anchor(reset);
    }

    .global-header__search__text{
        display: none;
    }

    .global-header__search__icon {
        @include betatype();
        color: $color-black;
    }
}

.header__site-search__modal{
    @include full-screen;
 
    @include media('<=small') {
        display: flex;
        justify-content: center;
        padding-left: $measure--basic;
        padding-right: $measure--basic;
    }

    @include media('>medium') {
        display: grid;
        @include x-column-grid(18);
    }
     
    form {
        grid-column: 5/15;
    }

    .header__site-search__modal__input-fieldset{
        display: flex;
        justify-content: center;
    }
 }

 .header__site-search__nav-button{
    .header__site-search__nav-button__text{
        display: none;
    }
 }

.js{
    .header__site-search{
        .header__site-search__nav-button[aria-expanded="false"] +.header__site-search__modal{
            display: none;
        }

        .header__site-search__nav-button[aria-expanded="true"] +.header__site-search__modal{
            position: absolute;
            width: 100vw;
            z-index: z-index(above);
            border-top: $border-radius-thin solid $color-gray80;
            padding-top: $measure--medium3;
            padding-bottom: $measure--medium3;
            background-color: $color-white;
            max-width: initial;
            bottom: -$measure--huge5;
            right: 0;

            @include media('>small'){
                right: -$measure--half;
            }
            @include media('>medium') {
                bottom: calc($measure--huge5 - $measure--basic - $measure--basic);
            }
            @include media('>extralarge') {
                right: calc(#{-$measure--half} - ((100vw - 1440px) / 2));
            }
            @include media('>1080p'){
                right: calc(-0rem - ((100vw - 100%) / 2));
            }
        }
    }

    .has__secondary-navigation{
        .header__site-search__nav-button[aria-expanded="true"] +.header__site-search__modal{
            @include media('>medium') {
                bottom: calc(-#{$measure--huge5} - #{$measure--basic} - #{$measure--basic} - (#{$font-size--mini} * 1.625) - #{$border-width-default});
            }
        }
        
    }
}

.header__site-search__modal__input-fieldset{
    border-style: initial;
    border-width: initial;
    border-color: initial;
}

.header__site-search__modal__input__wrapper{
    width: 100%;
    margin-right: $measure--basic;
    position: relative;

    &:focus-within{
        outline-style:  $border-style-default;
        outline-width: $border-width-outline;
        outline-color: $color-black;
        outline-offset: $border-width-thick-w20;
    }
}

.header__site-search__modal__input{
    font-family: $font-family-default;
    font-size: $font-size-default;
    font-weight: $font-weight-default;
    height: $measure-xlarge;
    margin: 0;
    padding: $measure-small;
    width: 100%;
    background-color: $color-white-smoke;
    border: $border-width-default $border-style-default $color-black;

    &:focus-visible{
        outline: none;
    }
}

.header__site-search__modal__input{

    ~.header__site-search__modal__input__suggestions.show{
        display: block;
    }
}

.header__site-search__modal__input__clear-button{
    position: absolute;
    right: 0;
    top: 0;
    display: none;

    span{
        pointer-events: none;
    }   
}

.header__site-search__modal__input.active{
    ~ .header__site-search__modal__input__clear-button{
        display: block;
    }
}

.header__site-search__modal__query__submit{
    @extend .button--icon;
    width: $measure-xlarge;
    height: $measure-xlarge;
    transition-property: background-color, outline;
}

.header__site-search__modal__input__suggestions{
    display: none;
    position: absolute;
    top: $measure--medium3;
    width: 100%;
    background-color: $color-white-smoke;
    text-align: left;
}

.header__site-search__modal__input__suggestions{
    button{
        width: 100%;
        text-align: left;

        span{
            pointer-events: none;
        }

        &:hover {
            background-color: $color-corporate-lead;
        }

        &:focus{
            outline-style:  $border-style-default;
            outline-width: $border-width-outline;
            outline-color: $color-black;
            outline-offset: $border-width-thick-w20;
        }
    }
}

.header__site-search__modal__input.show:has(~ .header__site-search__modal__input__suggestions){
    &:focus{
        outline-style:  $border-style-default;
        outline-width: $border-width-outline;
        outline-color: $color-black;
        outline-offset: $border-width-thick-w20;
    }
}

.header__site-search__modal__input__suggestions__key{
    font-weight: bold;
}

.search-overlay__active{
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: z-index(modal);
    background-color: rgba(30,30,30,.8);
}
