////
/// CMS editor warning
/// @group patterns
////

.cms-editor-warning {
    display: none;
}

.cms-editor {
    .cms-editor-warning {
        display: flex;
        justify-content: center;

        p {
            background-color: $color-cmswarning;
            color: $color-white;
            padding-left: $measure-small;
            padding-right: $measure-small;
            margin-top: $measure-large;
            margin-bottom: $measure-small;
            max-width: $max-line-length;
            padding-top: $measure-small;
            padding-bottom: $measure-xlarge;
            position: relative;

            &:after {
                bottom: $measure-small;
                content: 'CMS editor warning';
                font-weight: $font-weight-light;
                position: absolute;
                right: $measure-small;
            }
        }
    }
}
