.banner-vi--v23.banner-vi--image {
    position: relative;
    overflow: hidden;

    .banner-vi__image {
        position: relative;
        width: 100%;
        overflow: hidden;

        @include media('>between') {
            max-height: 32rem;
        }

        @include media('>4K') {
            max-height: 45rem;
        }

        img {
            width: 100%;
        }
    }

    .banner-vi__container__wrapper {
        position: absolute;
        top: 0;
        bottom: 0;
        width: 100%;

        @include media('<=between') {
            position: relative;
        }
    }

    .banner-vi__container {
        flex-direction: row;
    }

    .banner-vi__container__column {
        flex-basis: 50%;
        align-items: center;
        display: flex;
        height: 100%;

        @include media('<=between') {
            flex-basis: 100%;
            align-items: center;
            flex-direction: column;
        }
    }

    .banner-vi__content {
        display: flex;
        flex-direction: row;
    }

    .banner-vi__content__text {
        padding-top: $measure-xlarge * 2.5;
        padding-bottom: $measure-xlarge * 2.5;
        align-items: center;
        display: flex;
        width: inherit;
        flex-basis: 85%;
        left: -3%;

        @include media('<=between') {
            padding: 0;
            flex-basis: 100%;
            left: 0;
        }
    }

    .banner-vi__content__text__item {
        @include media('<=between') {
            width: 100%;
        }
    }

    .banner-vi__shape {
        position: relative;

        @include media('<=between') {
            display: none;
        }
    }

    .banner-vi__shape__mobile__wrapper {
        position: absolute;
        z-index: z-index(default);
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;

        @include media('>between') {
            display: none;
        }
    }

    .banner-vi__shape__mobile {
        position: relative;
        height: 101%;

        &.banner-vi__shape__mobile--left {
            width: auto;
            height: 70%;
            left: 0;

            svg {
                left: $measure-medium;
            }
        }

        &.banner-vi__shape__mobile--right {
            width: inherit;
            height: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: center;

            svg {
                right: 0;
            }
        }
    }

    .banner-vi__shape--left,
    .banner-vi__shape__mobile--left {
        flex-basis: 15%;
        height: inherit;

        svg {
            position: absolute;
            height: 101%;
            width: inherit;
        }
    }

    .banner-vi__content--right {
        left: inherit;

        @include media('<=between') {
            flex-direction: column;
            width: auto;
        }
    }

    .banner-vi__shape--right,
    .banner-vi__shape__mobile--right {
        height: 100%;
        width: 100%;

        svg {
            transform: rotate(180deg);
            position: relative;
            height: 101%;
            width: auto;
            right: 0;

            path {
                fill: $color-white;
            }
        }

        @include media('>between') {
            &:after {
                content: '';
                position: absolute;
                background-color: white;
                height: 100%;
                width: 100vw;
                left: 99%;
            }
        }
    }

    .banner-vi__shape--right {
        display: flex;
        justify-content: flex-end;
        align-items: center;

        @include media('<=between') {
            display: none;
        }
    }

    .image-credit__wrap {
        height: 100%;
        flex: 1;
        max-width: 70%;
        display: flex;
        justify-content: flex-end;
    }

    .image-credit {
        height: 100%;
        position: relative;
        color: inherit;
        font-size: 1em;
        min-width: $measure-medium * 3;
        color: $color-gainsboro;
        font-weight: $font-weight-bold;

        @include media('<=medium') {
            min-height: $measure-medium * 2;
            min-width: $measure-medium * 2;
        }

        span:after {
            background-color: $color-zzz;
            font-family: 'FontAwesome';
            content: '\f05a';
            position: absolute;
            right: 0;
            width: $measure-medium * 3;
            height: $measure-medium * 3;
            display: flex;
            justify-content: center;
            align-items: center;
            color: $color-white;

            @include media('<=medium') {
                height: $measure-medium * 2;
                width: $measure-medium * 2;
            }
        }

        .image-credit__checkbox {
            position: absolute;
            right: 0;
            z-index: z-index(above);
            margin: 0;
            width: $measure-medium * 3;
            height: $measure-medium * 3;
            opacity: 0;
            cursor: pointer;

            @include media('<=medium') {
                height: $measure-medium * 2;
                width: $measure-medium * 2;
            }

            &:checked ~ .image-credit__text {
                display: flex;
            }
        }
    }

    .image-credit__text {
        background-color: #222;
        display: none;
        padding-right: $measure-medium * 3;
        display: none;
        align-items: center;
        min-height: $measure-medium * 3;

        @include media('<=medium') {
            min-height: $measure-medium * 2;
        }
    }

    &.banner-vi--image--promo {
        .banner-vi__content__text {
            h1 {
                @include media('<=between') {
                    text-align: left;
                }
            }
        }

        .banner-vi__content--right {
            @include media('<=between') {
                border: $border-width-hairline $border-style-default
                    $color-fordgray;
                width: 100%;
                z-index: z-index(default);
            }
        }

        .banner-vi__shape--right svg :after {
            @include media('<medium') {
                left: 75%;
            }
        }

        .banner-vi__container__column:nth-child(2) {
            @include media('<=between') {
                padding: $measure-small $measure-medium;
                position: relative;

                &::after {
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 50%;
                    top: 0;
                }
            }
        }
    }

    .promo-cta h2,
    .promo-cta p {
        line-height: $line-height-default;
    }

    .promo-cta.promo-cta--small {
        @include media('>between') {
            padding: 0;
        }
    }

    .promo-cta__button {
        display: inline-flex;
        width: inherit;
        align-items: center;

        .promo-cta__button__icon {
            position: static;
        }
    }

    &[data-animated='yes'] {
        @include media('>between') {
            .banner-vi__shape {
                animation: brackets-slide-in ease-out 1s;
                @keyframes brackets-slide-in {
                    0% {
                        left: -90vw;
                    }
                    100% {
                        left: 0;
                    }
                }
            }

            &.banner-vi--image--promo .banner-vi__shape {
                animation: brackets-slide-in-alt ease-out 1s;
                @keyframes brackets-slide-in-alt {
                    0% {
                        left: -70vw;
                    }
                    100% {
                        left: 0;
                    }
                }
            }

            .banner-vi__content.banner-vi__content--right {
                animation: promo-slide-up ease-out 1.5s;
                @keyframes promo-slide-up {
                    0% {
                        top: 100%;
                    }
                    66% {
                        top: 100%;
                    }
                    100% {
                        top: 0;
                    }
                }
            }

            .image-credit__wrap {
                animation: image-credit-fade-in linear 1.5s;
                @keyframes image-credit-fade-in {
                    0% {
                        opacity: 0;
                    }
                    66% {
                        opacity: 0;
                    }
                    100% {
                        opacity: 1;
                    }
                }
            }
        }
    }
}

