.teaching-staff-v23 {
    .featured-profile-v23 {
        border-left-width: $border-width-thick;
        border-left-style: $border-style-default;
        border-left-color: $color-mba-accent-v23;
        padding-left: $measure--small2;
        display: flex;
        flex-direction: row;
        margin-bottom: $measure--small2;
        max-width: $max-line-length;
        padding-top: $measure--basic;
        align-items: flex-start;
        gap: $measure--basic;

        @include media('<=tablet') {
            flex-direction: column-reverse;
            gap: initial;
        }
    }

    .featured-profile-v23__heading {
        margin-bottom: $measure--half;
        font-size: $font-size-gamma;
    }

    .featured-profile-v23__title {
        font-size: $font-size-delta;
        font-weight: $font-weight-book;
        color: $color-emperor;
        @include font-family(heading);
        line-height: $line-height-small;
    }

    .featured-profile-v23__block {
        padding: 0 0 0 $measure--threequarters;
        margin-bottom: $measure--small2;
        font-size: inherit;
        font-weight: inherit;
        color: $color-black;

        &:before {
            content: '';
            left: 0;
            position: absolute;
            margin: inherit;
            height: inherit;
            width: inherit;
            background-color: inherit;
            mask: inherit;
        }

        &:after {
            content: '';
            right: auto;
            position: absolute;
            bottom: 0;
        }

        span {
            @include font-family(meta);
        }
    }

    .featured-profile-v23__img {
        position: relative;
        margin-bottom: $measure--basic;
        display: flex;
        flex-direction: row;
        padding-right: $measure--medium3;
        
        &:before {
            content: '';
            width: $measure--huge1;
            height: 100%;
            position: absolute;
            z-index: z-index(default);
            left: 0;
            top: 0;

            border: $border-width-default-w20 $border-style-default
                $color-mba-accent-v23;
        }

        &:after {
            content: '';
            width: $measure--medium1;
            height: 107%;
            z-index: z-index(default);
            position: absolute;
            right: 0;
            top: 0;

            background: radial-gradient(
                ellipse at center,
                $color-fordgray,
                $color-fordgray $border-width-hairline-w20,
                transparent 0,
                transparent 100%
            );
            background-size: $measure--small1 $measure--small1;
        }

        picture {
            width: $measure--huge1;
            top: $measure--half;
            left: $measure--half;
        }

        img {
            width: 100%;
        }
    }

    .teaching-staff-v23__profiles-lists--column-two {
        display: flex;
        flex-direction: row;
        gap: $measure--basic;

        @include media('<=large') {
            flex-direction: column;
            gap: 0;
        }

        .teaching-staff-v23__profiles-list {
            flex-basis: 50%;
            @include media('<=large') {
                flex-basis: 100%;
            }
        }
    }

    .teaching-staff-v23__profiles-list {
        list-style-type: disc;
        margin-left: $measure--basic;
    }
}
