
@include media('<=between'){
    .banner-new,
    .banner-no-img{
        &.banner-promo{
            .banner__image__group{
                overflow: initial;
            }
            .banner__content {
                .banner__content__text__item{
                    margin-bottom: initial;
                }
            }
        }
        .banner__content__promo__wrapper{
            position: relative;
        }
        .banner__content__promo--bg{
            &:before{
                content: '';
                position: absolute;
                width: 100%;
                height: 50%;
                top: 0;
            }
            &:after{
                content: '';
                position: absolute;
                width: 100%;
                height: 50%;
                bottom: 0;
            }
        }
    }
}

.banner-new,
.banner-no-img-varieties.banner-no-img{
    .banner__container{
        @include media('<=between'){
            padding-left: initial;
            padding-right: initial;
            display: block;
            margin-left: initial;
            margin-right: initial;
            max-width: initial;
        }
    }
    .banner__content {
        @include media('<=between'){
           padding-left: initial;
           padding-right: initial;
        }
        @include grid-column-push-map(
            (
                'small': 0,
                'extralarge': 1,
            )
        );
    
        .banner__content__promo {
            left: 0;
    
            @include media('>large') {
                width: 25%;
            }
    
            @include media('>medium', '<=large') {
                width: 27%;
            }
    
            @include media('>between', '<=medium') {
                width: grid-column-width(4);
            }
    
            @include media('>small', '<=between') {
                width: grid-column-width(6);
            }
    
            @include media('<=between') {
                width: auto;
                margin-left: #{$measure-medium / 2};
                margin-right: #{$measure-medium / 2};
            }

            @include media('<=between') {
                bottom: initial;
                margin-bottom: $measure-medium;
            }
    
            @include media('>medium') {
                position: absolute;
                bottom: -#{$measure-medium * 2};
                z-index: z-index(navigation);
            }
        }
    
        .banner__content__text {
            @include media('<=between'){
                padding-top: initial;
                padding-left: initial;
                padding-right: initial;
                margin-bottom: initial;
            }
            @include media('<=between'){
                h1{
                    width:100%;
                }
            }
        }
    
        .banner__content__text__item {
            @include media('<=between'){
                margin-bottom: initial;
            }
        }
    
        h1,
        .banner__content__text__subtitle {
            display: inline-block;
            margin: 0;
            padding: $measure-small $measure-medium;
            width: auto;
            font-size: $font-size-beta;
            @include font-family(meta);
        }
    
        .banner__content__text__subtitle,
        .banner__content__text__standfirst {
            font-weight: $font-weight-light;
        }
    
        .banner__content__text__standfirst {
            font-size: $font-size-gamma;
        }
    
        @include media('<=medium') {
            .banner__content__text__subtitle,
            .banner__content__text__standfirst {
                display: none;
            }
        }
    
        h1 {
            font-weight: $font-weight-book;
        }
    }
}

.banner-new.banner--cutout--left,
.banner-no-img-varieties.banner-no-img {
    .banner__content .banner__content__promo{
        right: 0;
        left: auto;

        @include media('>between', '<=medium') {
            margin-left: auto;
        }

        @include media('>medium') {
            right: 7rem;
        }

        @include media('>extralarge') {
            right: 0;
        }
    }
}

@each $theme, $colors in $banner-themes {
    $title-background: nth($colors, 1);
    $title-color: nth($colors, 2);
    $subtitle-background: nth($colors, 3);
    $subtitle-color: nth($colors, 4);

    .banner-new.banner--theme--#{$theme},
    .banner-no-img-varieties.banner-no-img.banner--theme--#{$theme} {
        .banner__content__text__item:first-child{
            @include media('<=between'){
                background-color: $title-background;
            }
        }
        .banner__content__promo__wrapper{
            .banner__content__promo--bg{
                @include media('<=between'){
                    &:before{
                        background-color: $title-background;
                    }
                    &:after{
                        background-color: $color-white;
                    }
                }
            }
        }
    }
}
