.link__external {
    &:after {
        font-family: $font-family-icon;
        content: unquote('"#{ $fa-var-up-right-from-square }"');
        font-weight: 900;
        padding-left: 0.3rem;
        font-size: 12px;
    }
}

.link__pdf {
    &::before {
        font-family: $font-family-icon;
        font-size: $font-size-default;
        content: fa-content($fa-var-file-pdf);
        font-weight:$font-weight-default; 
        padding-right: 0.5rem;
    }
}
