.courses-v23 {
    .courses-v23__entry-requirements {
        .entry-req-v23__heading {
            max-width: initial;
        }
    }
}

.courses-v23__entry-requirements {
    .accordion-v23 {
        grid-column: 1/12;
    }
    
    .entry-req-v23 {
        @include font-family(meta);
        width: 100%;
        display: flex;
        flex-direction: column;
        padding-top: $measure--basic;
        padding-bottom: $measure--basic;
        border: $measure--small1 solid $color-ug-lead;

        @include media('<=tablet') {
            padding: $measure--basic $measure--basic;
        }

        @include media('>=medium') {
            padding-top: $measure--small2;
            padding-bottom: $measure--small2;
        }
    }

    .entry-req-v23__heading {
        @include h3style();
        text-align: center;
        margin-bottom: initial;
    }

    .entry-req-v23__list {
        display: flex;
        flex-direction: row;
        padding: $measure--small4 $measure--basic $measure--small4 $measure--basic;

        @include media('<=tablet') {
            flex-direction: column;
            padding: initial;
        }
    }

    .entry-req-v23__item {
        flex: 1;
        border-right: $border-style-default $border-width-hairline $color-ug-lead;
        text-align: center;
        padding: $measure--basic;
        max-width: initial;

        @include media('<=tablet') {
            border-right: none;
            border-bottom: $border-style-default $border-width-hairline $color-ug-lead;
            padding: $measure--small4 0;
        }

        .entry-req-v23__item__first-line {
            @include alphatype();
            font-weight: $font-weight-medium;
        }

        .entry-req-v23__item__second-line {
            @include font-family('meta');
            color: $color-ug-accent;
            font-size: $font-size--theta;
            font-weight: $font-weight-medium;
        }

        .entry-req-v23__item__third-line {
            @include alphatype();
            font-weight: $font-weight-medium;
        }

        &:last-of-type {
            border-right: none;

            @include media('<=tablet') {
                border-bottom: none;
            }
        }
    }
}

// MBA specific override
.mba-courses-v23 {
    .courses-v23__entry-requirements {
        .accordion-v23 {
            grid-column: 1/12;
        }
    }
}
