@mixin styled-dropdown {
    //Add this to a wrapper of a <select>
    position: relative;
    border-radius: 0.25rem;
    background-color: #ffffff;
    z-index: 0;
    height: 100%;
    



    &:after {
        @include filter-box-down-arrow;
         z-index: z-index(below);
     }

     select {
        width: 100%;
        appearance: none;
        @include filter-field($with-icon:true);
        border-radius: $border-radius-default;
        border: $border-width-default $border-style-default $color-black;
        background-color: transparent;
        height: 100%;
        border-radius: $border-radius-default;
        border: $border-width-default $border-style-default $color-black;
        &:focus {
            outline: $border-width-outline $border-style-default $color-black;
            outline-offset: $border-width-thick-w20;
        }

        &:active {
            //Some browser ignore this.
            outline: $border-width-outline  dotted $color-black;
            outline-offset: $border-width-thick;
        }

        @include media('>=extralargePlusSides') {
            @include dynamicFontSize4k();
        }
     }
}



@mixin styled-input-box {
    width: 100%;
    list-style: none !important;
    box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    appearance: none;
    border-color: $color-quill;
    @include filter-field;
}

@mixin filter-field($active-bg: false, $with-icon: false) {
    font-size: 16px;
    padding: 11px;
    border: 1px solid $color-nobel;
    border-radius: 0;
    background: transparent;

    @if $active-bg {
        &:hover,
        &:focus,
        &:active {
            background: $color-gallery;
        }
    }
    @if $with-icon {
        padding-right: $measure-small * 6;
    }
    @include media('>=extralargePlusSides'){
        @extend %content-elements !optional;
        @include dynamicFontSize4k();
        padding: $measure--threequarters;
        @if $with-icon {
            padding-right: $measure-small * 6;
        }
    }
}

@mixin filter-box-down-arrow() {
    @include font-family(icon);
    content: unquote('"#{ $fa-var-chevron-down }"');
    font-size: $font-size-delta;
    position: absolute;
    right: 0;
    color: $color-white;
    background-color: $color-black;
    border-radius: 50%;
    font-weight: 900;
    text-align: center;
    height: $measure-small * 3;
    width: $measure-small * 3;
    transform: translateY(-50%);
    font-size: $font-size-small;
    padding-top: 0.15rem;
    line-height: $line-height-default;
    display: flex;
    margin-right: $measure-small;
    top:50%;
    justify-content: center;
    align-items: center;

    @include media('>=extralargePlusSides') {
        @include dynamicFontSize4k();
    }
}








@mixin person-profile {
    display: flex;
    flex-direction: row;
    background-color: #fafafa;
    box-shadow: 0 8px 16px 0 rgba(30, 30, 30, 0.2);
    -webkit-box-shadow: 0 8px 16px 0 rgba(30, 30, 30, 0.2);
    padding: 1rem;
    margin-bottom: 1rem;
}

@mixin grid-items($columns) {
    @include media('>medium') {
        display: grid;
        grid-template-columns: repeat($columns, 1fr);
        grid-column-gap: calc(#{$measure-medium + $measure-small});
    }
}

@mixin text-highlight(
    $txt-color: $color-white,
    $bg-color: $color-corporate-red
) {
    color: $txt-color;
    background-color: $bg-color;
    box-shadow: $measure--half 0 0 $bg-color, -$measure--half 0 0 $bg-color;
    display: inline;
    box-decoration-break: clone;
    margin-left: $measure--half;
}

@mixin full-screen {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;

    @include media('<=small') {
        padding-left: $grid-padding-half;
        padding-right: $grid-padding-half;
    }

    @include media('>small') {
        padding-left: $grid-padding;
        padding-right: $grid-padding;
    }

    @include media('>extralarge') {
        max-width: $grid-width-extra-large;
    }

    @include media('>1080p') {
        max-width: 83.333%;
    }
}

@mixin bracket-block($bracket-color: $color-corporate-lead) {
    .bracket-block__anglesA {
        &:before {
            content: ' ';
            position: absolute;
            width: $measure--medium3;
            height: 100%;
            border-style: solid;
            border-color: $bracket-color;
            border-width: $measure--small2 0 0 $measure--small2;
            left: -0;
            top: -0;
        }

        &:after {
            content: ' ';
            position: absolute;
            width: $measure--medium3;
            height: 100%;
            border-style: solid;
            border-color: $bracket-color;
            border-width: 0 0 $measure--small2 $measure--small2;
            left: -0;
            top: 0;
        }
    }

    .bracket-block__anglesB {
        &:before {
            content: ' ';
            position: absolute;
            width: $measure--medium3;
            height: 100%;
            border-style: solid;
            border-color: $bracket-color;
            border-width: $measure--small2 $measure--small2 0 0;
            right: -0;
            top: -0;
        }

        &:after {
            content: ' ';
            position: absolute;
            width: $measure--medium3;
            height: 100%;
            border-style: solid;
            border-color: $bracket-color;
            border-width: 0 $measure--small2 $measure--small2 0;
            right: -0;
            top: 0;
        }
    }

    @include media('<=medium') {
        .bracket-block__anglesA {
            &:before {
                left: -$measure--small4;
                border-width: $measure--basic 0 0 $measure--basic;
                width: $measure--small4;
            }
            &:after {
                left: -$measure--small4;
                border-width: 0 0 $measure--basic $measure--basic;
                width: $measure--small4;
            }
        }
        .bracket-block__anglesB {
            &:before {
                right: -$measure--small4;
                border-width: $measure--basic $measure--basic 0 0;
                width: $measure--small4;
            }
            &:after {
                right: -$measure--small4;
                border-width: 0 $measure--basic $measure--basic 0;
                width: $measure--small4;
            }
        }
    }
}

@mixin custom-unordered-list($font, $color: $color-corporate-accent-2) {
    // have to be added to <ul> rule
    list-style-type: none;
    margin-left: 0;

    li {
        position: relative;
        padding-left: $measure--small2;
        list-style: none;
    }

    li:before {
        display: block;
        font-family: $font-family-icon;
        content: $font;
        color: $color;
        padding-right: $measure--half; // 0.5rem
        position: absolute;
        left: 0;
    }
}

@mixin screenreader-only-values {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

@mixin screenreader-only {
    @include screenreader-only-values;
}

.sr-only {
    @include screenreader-only;
}

