@mixin bracket-border-v23($color: inherit, $size: 0.75rem) {

    position: relative;
    padding:$size;
    &::before, &::after {
        content: '';
        border-style: solid;
        border-color:$color;
        height: 100%;
        width: $size * 2;
        position: absolute;
        top: 0;
    }
    &::after {
        right: 0;
        border-width: $size $size $size 0;
    }

    &::before {
        left: 0;
        border-width: $size 0 $size $size;
    }


}


// Apply  corner border, allows customisation for top-left corner, bottom-right corner, or both.
//
// Parameters:
// $color: The color of the border.
// $corners: Specifies which corners to apply the border to. Accepts 'top-left', 'bottom-right', or 'both'. Defaults to 'both'.
//
// Usage:
// @include corner-border-v23($color) - Applies the gradient to both corners.
// @include corner-border-v23($color, 'top-left') - Applies the gradient only to the top-left corner.
@mixin corner-border-v23($color, $corners: both) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    // Conditionally applies gradients based on the $corners argument.
    background: if($corners == both or $corners == top-left, linear-gradient(
                to right,
                $color $measure--threequarters,
                transparent $measure--threequarters
            )
            0 0, null),
        if($corners == both or $corners == top-left, linear-gradient(
                to bottom,
                $color $measure--threequarters,
                transparent $measure--threequarters
            )
            0 0, null),
        if($corners == both or $corners == bottom-right, linear-gradient(
                to left,
                $color $measure--threequarters,
                transparent $measure--threequarters
            )
            100% 100%, null),
        if($corners == both or $corners == bottom-right, linear-gradient(
                to top,
                $color $measure--threequarters,
                transparent $measure--threequarters
            )
            100% 100%, null);
    background-repeat: no-repeat;
    background-size: $measure--small2 $measure--small2;
    pointer-events: none; 
}

