/*
* Styles for arrow and responsive slider
*/
.js {
    .slider-v23[data-style='responsive'] {
        --slider-grid-columns: 3; //Default columns, this is overwritten by the slider JS
        display: flex;
        margin-right: 0;
        overflow: hidden;
        position: relative;
        width: calc(100% + 1.5rem);
        margin-left: -0.75rem;
        margin-bottom: $measure--basic;

        .slide {
            padding: $measure-smallish;
        }

        @include media('>=between') {
            &[data-style='buttons'] {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
            }
        }

        &[data-optimised='true'] {
            li {
                ul li {
                    a {
                        height: 100%;
                    }
                }
            }
        }
        .slider-v23__cards-inner {
            height: 100%;
        }
        .slide {
            top: 0;
            transition: left 1s ease-in-out;
            width: 100%;
            list-style: none;
            max-width: inherit;

            ul {
                display: grid;
                max-width: 100%;
                grid-template-columns: repeat(var(--slider-grid-columns), 1fr);
                list-style-type: none;

                li {
                    flex-basis: 50%;

                    &:nth-child(2) {
                        margin-bottom: 0;
                    }

                    &:last-of-type {
                        margin-bottom: 0;
                    }
                }
            }

            &:focus {
                @include outline-properties($color-black);
                outline-offset: -#{$border-width-thick};
            }

            &:last-of-type {
                margin-bottom: 0;
            }

            &[data-sliderposition='-1'] {
                left: -100%;
                position: absolute;
            }

            &[data-sliderposition='0'] {
                left: 0;
                position: relative;
            }

            &[data-sliderposition='1'] {
                left: 100%;
                position: absolute;
            }
        }

        //  Edge case example to hide controls is responsive slider present with 1 slides containing 2 items
        &[data-count='1']:not([data-small='true']) {
            @include media('>=tablet') {
                & + .slider-v23__controls {
                    display: none;
                }
            }
        }

        & + .slider-v23__controls {
            margin-top: 0;
            button {
                border: none;
            }
        }
    }

    // Edge case: responsive/arrow slider with 1 item where slider doesn't load
    .slider-v23__cards[data-style='responsive']:not(.slider-v23) {
        @include media('>=tablet') {
            justify-content: initial;
            grid-template-columns: repeat(auto-fill, minmax(31%, 1fr));
            grid-gap: $measure--small2;
        }
    }
}
