// Currently only used on MBA course pages
.mba-courses-v23 {
    .timeline-v24  {
        grid-column: 1/-1;
        margin-top: $measure--basic;
        
        table {
            width: 100%;
            border: none;

            tbody {

                tr {
                    display: flex;
                    background-color: inherit;
    
                    &:hover, &:focus, &:active { 
                        background: inherit;
                    }

                    &:hover td {
                        background-color: $color-white;
                    }
    
                    td, th {
                        border: none;
                        flex-basis: 25%;
                        margin-right: $measure--half;
                    }
                }
            }
        }
    }
}

.timeline-v24 {
    background-color: $color-zircon-green;
    padding: $measure--basic;

    .timeline-v24__year__heading {
        margin-top: $measure--basic;
    }

    .timeline-v24__heading {
        background-color: $color-black;
        color: $color-white;
        padding: $measure--half $measure--threequarters;

        span {
            display: block;

            &:first-child {
                text-transform: uppercase;
                @include font-family(meta);
                font-weight: $font-weight-bold;
                @include smalltype();
            }

            &:nth-of-type(2) {
                @include extrasmalltype();
                font-weight: $font-weight-default;
            }
        }
    }

    .timeline-v24__content__wrap {
        background-color: $color-white;
    }

    .timeline-v24__content {
        margin-top: $measure--half;
        background-color: $color-white;
        @include extrasmalltype();
        
        p, li, ul {
            @include extrasmalltype();
        }

        ul {
            margin-bottom: 0;
        } 
    }

    .timeline-v24__tags__wrap {
        background-color: $color-white;
    }

    .timeline-v24__tag {
        background-color: $color-mba-lead;
        color: $color-white;
        @include extrasmalltype();
        font-weight: $font-weight-medium;
        margin-top: $measure--half;
        padding: $measure--half $measure--threequarters;
    
        &.timeline-v24__tag--mba-accent {
            background-color: $color-mba-accent;
            color: $color-black;
        }

        &.timeline-v24__tag--grey {
            background-color: $color-gray80;
            color: $color-black;
        }  
    }

    .timeline-v24__desktop {
        @include media('<tablet') {
            display: none;
        }
    }

    .timeline-v24__tablet {
        @include media('>tablet') {
            display: none;
        }

        .timeline-v24__terms {
            display: flex;
            flex-direction: column;
        }
    
        .timeline-v24__term {
            display: flex;
            flex-direction: row;
    
            @include media('<mobile') {
                flex-direction: column;
            }
        }
    
        .timeline-v24__tablet-headings {
            display: flex;
            flex-direction: row;

            .timeline-v24__heading:nth-of-type(2) {
                flex-basis: 66%;
                margin-right: 0;
            }
    
            @include media('>medium') {
                display: none;
            }
    
            @include media('<mobile') {
                display: none;
            }
        }

        .timeline-v24__heading {
            flex-basis: 33%;
            margin-top: $measure--half;
            margin-right: $measure--half;

            @include media('<mobile') {
                display: flex;
                flex-direction: row;
                margin-right: 0;
                justify-content: space-between;
            }
        }
        
        .timeline-v24__content__wrap {
            height: 100%;
            margin-top: $measure--half;
            padding: $measure--threequarters;
            flex-basis: 66%;
        }
    }

    &.timeline-v24__show-more {
        position: relative;
        overflow: hidden;

        &[data-open='false'] {
            .timeline-v24__show-more__content {
                max-height: var( --sm-closed-height);
            }
        }

        &[data-open='true'] {
            padding-bottom: $measure--medium8;

            .timeline-v24__show-more__gradient {
                background: initial;
            }
        }

        .timeline-v24__show-more__content {
            overflow: hidden;
            max-height: var( --sm-open-height);
            transition: max-height 0.2s ease-in;
        }

        .timeline-v24__show-more__button__container {
            position: absolute;
            margin-top: $measure--basic;
            display: flex;
            flex-direction: column;
            justify-content: center;
            bottom: 0;
            width: 100%;
        }

        .timeline-v24__show-more__gradient {
            height: $measure--medium11;
            width: 100%;
            background: linear-gradient(0deg, rgba(236,238,238,1) 0%, rgba(255,255,255,0) 100%);
        }

        .timeline-v24__show-more__button__wrap {
            background-color: $color-zircon-green;
            text-align: center;
            padding-bottom: $measure--small1;
        }

        .timeline-v24__show-more__button {
            background-color: $color-black;
            color: $color-white;
            border-radius: $measure--small1;
            padding: $border-width-default $measure--small1;
            text-decoration: underline;
            @include font-family(meta);
            @include extrasmalltype();
            font-weight: $font-weight-medium;

            &:focus {
                outline-offset: $border-width-default;
            }
        }
    }
}
