.courses-v23__careers__recent-employers {
    padding: $measure--small4 $measure--basic;
    background: radial-gradient(
        ellipse at center,
        $color-nobel,
        $color-nobel $border-width-hairline-w20,
        transparent 0,
        transparent 100%
    );
    background-size: $measure--small1 $measure--small1;

    ul {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-column-gap: $measure--small2;
        grid-row-gap: $measure--small2;
        list-style: none;
        margin-left: initial;
        margin-bottom: initial;

        @include media('<small') {
            grid-column-gap: $measure--basic;
            grid-row-gap: $measure--basic;
        }

        @include media('>large') {
            grid-template-columns: repeat(4, 1fr);
        }

        li {
            margin-top: initial;
            margin-right: initial;

            img {
                background-color: $color-white;
                width: 100%;
                max-width: 16rem;
            }
        }
    }
}
