.promo-box-v23__wrapper {
    @include media('<small') {}

    .promo-box-v23 {
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
        padding: $measure--small4 $measure--medium3;
    }

    .promo-box-v23__heading__wrap {
        &:after {
            content: '';
            display: block;
            height: $measure--quarter;
            margin-left: auto;
            margin-right: auto;
            margin-top: $measure--small2;
            margin-bottom: $measure--small2;
            width: $measure--medium11;
        }
    }

    .promo-box-v23__heading {
        font-weight: $font-weight-bold;
        text-align: center;
        font-size: $font-size-gamma;
    }

    .promo-box-v23__cta {
        font-size: $font-size-gamma;
        display: flex;
        flex-direction: revert;
        position: relative;
        align-items: center;
        margin-top: $measure--small2;
        font-size: 1.44rem;
    }

    .promo-box-v23__cta__link {
        display: flex;
        flex-direction: row;
        position: relative;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        font-weight: $font-weight-bold;
    }
}

.ug-courses-v23 {
    .promo-box-v23__cta__link {
        @include anchor-cta--alto();
    }
}

.pg-courses-v23 {
    .promo-box-v23__cta__link {
        @include anchor-cta--prussian();
    }
}

@each $level, $color in $courses-v23-palette {
    $lead-color: nth($color, 8);
    $lead-text: nth($color, 2);
    $cta-main-bg: nth($color, 10);

    .#{$level}-courses-v23 {
        .promo-box-v23 {
            .promo-box-v23__heading__wrap:after {
                background: $lead-color;
                color: $lead-text;
            }
        }
    }
}