.banner-vi--text {
    position: relative;  

    .banner-vi__shape {
        position: absolute;
        height: $measure-small * 3;
        width: $measure-small * 3;

        @include media('<=between') {
            height: $measure-small;
            width: $measure-small;
        }

        &:nth-child(1) {
            transform: rotate(195deg);
            left: 6%;
            top: 50%;      
        }

        &:nth-child(2) {
            transform: rotate(290deg);
            left: 20%;
            top: 10%;
        }

        &:nth-child(3) {
            transform: rotate(90deg);
            left: 37%;
            top: 60%;
        }

        &:nth-child(4) {
            transform: rotate(-6deg);
            left: 52%;
            top: 35%;
        }

        &:nth-child(5) {
            transform: rotate(34deg);
            left: 67%;
            top: 48%;
        }

        &:nth-child(6) {
            transform: rotate(-75deg);
            left: 78%;
            top: 5%;
        }

        &:nth-child(7) {
            transform: rotate(105deg);
            left: 88%;
            top: 35%;
        }
    }

    &[data-animated="yes"] .banner-vi__shape {
        &:nth-child(1) {
            animation: shape-1-trajectory linear 120s infinite;

            @include media('>between') {
                @keyframes shape-1-trajectory {
                0% {left: 6%; top: 50%; opacity:0; }
                1%{opacity: 0.5}
                1.2%{opacity: 1}
                100% {left: 16%; top: -9%;}
                }
            }
        }

        &:nth-child(2) {
            animation: shape-2-trajectory linear 115s infinite;
            @include media('>between', '<=medium') {
                @keyframes shape-2-trajectory {
                    0% {left: 20%; top: 10%; opacity:0; }
                    1%{opacity: 0.5}
                    1.2%{opacity: 1}
                    100% {left: 31%; top: 80%;}
                }
            }

            @include media('>medium') {
                @keyframes shape-2-trajectory {
                    0% {left: 20%; top: 10%; opacity:0; }
                    1%{opacity: 0.5}
                    1.2%{opacity: 1}
                    100% {left: 31%; top: 100%;}
                }
            }
        }

        &:nth-child(3) {
            animation: shape-3-trajectory linear 125s infinite;
            @include media('>between') {
                @keyframes shape-3-trajectory {
                0% {left: 37%; top: 60%; opacity:0; }
                1%{opacity: 0.5}
                1.2%{opacity: 1}
                100% {left: 60%; top: -9%;}
                }
            }
        }

        &:nth-child(4) {
            animation: shape-4-trajectory linear 110s infinite;
            @include media('>between') {
                @keyframes shape-4-trajectory {
                0% {left: 52%; top: 35%; opacity:0; }
                1%{opacity: 0.5}
                1.2%{opacity: 1}
                100% {left: 44%; top: -9%;}
                }
            }
        }

        &:nth-child(5) {
            animation: shape-5-trajectory linear 115s infinite;
            @include media('>between') {
                @keyframes shape-5-trajectory {
                0% {left: 67%; top: 48%; opacity:0; }
                1%{opacity: 0.5}
                1.2%{opacity: 1}
                100% {left: 62%; top: -9%;}
                }
            }
        }

        &:nth-child(6) {
            animation: shape-6-trajectory linear 120s infinite;
            @include media('>between', '<=medium') {
                @keyframes shape-6-trajectory {
                    0% {left: 78%; top: 5%; opacity:0; }
                    1%{opacity: 0.5}
                    1.2%{opacity: 1}
                    100% {left: 64%; top: 80%;}
                }
             }
            
            @include media('>medium') {
                @keyframes shape-6-trajectory {
                    0% {left: 78%; top: 5%; opacity:0; }
                    1%{opacity: 0.5}
                    1.20%{opacity: 1}
                    100% {left: 64%; top: 100%;}
                }
            }
        }

        &:nth-child(7) {
            animation: shape-7-trajectory linear 120s infinite;
            @include media('>between') {
                @keyframes shape-7-trajectory {
                    0% {left: 88%; top: 35%; opacity:0; }
                    1%{opacity: 0.5}
                    1.2%{opacity: 1}
                    100% {left: 84%; top: -9%;}
                }
            }
        }
    }

    .banner-vi__content__text {
        margin-bottom: 0;

        @include media('>between') { 
            padding-top: $measure-large * 2.5;
            padding-bottom: $measure-large * 2.5;
        }
        
        @include media('<=between') { 
            margin-top: $measure-large * 5;
        }
    }
}
