////
/// Pathway Cards
/// @group components
////

.left-hand-navigation-grid,
.landing-page__content-container {
    .pathway-cards {
        grid-template-columns: repeat(2, 1fr);
    }
}

@each $level, $colors in $study-level-colors {
    $primary-color: nth($colors, 1);

    .pathway-cards--#{$level} .pathway-card__txt:only-child {
        border-top: $border-width-thick $border-style-default $primary-color;
    }
}

.pathway-cards {
    @include grid-items(3);
    @include media('>small','<=medium') {
        .pathway-card__content {
            display: flex;
            > div {
                flex: 1;
            }
        }
    }

    .pathway-card {
        margin-bottom: $measure-large;
        border: $border-width-hairline $border-style-default $color-concrete;
        height: fit-content;
        @include box-shadow(card, rgba($color-black, $opacity-faint));

        .pathway-card__content {
            @include transition(background-color $transition-duration ease-in-out);
        }
    }

    .pathway-card__anchor {
        &:focus {
            .pathway-card__content {
                outline: $border-width-outline $border-style-default $color-black;
                background-color: $color-concrete;
            }
        }
    
        &:active {
            .pathway-card__content {
                outline: $border-width-default $border-style-alternate $color-black;
            }
        }
    
        &:hover {
            .pathway-card__img {
                img {
                    transform: scale(1.02);
                }
            }
            .pathway-card__content {
                background-color: $color-concrete;
            } 
        }
    }

    .pathway-card__img {
        overflow: hidden;
        img {
            width: 100%; // Ensures image occupies full width on narrow viewports
            @include transition(transform $transition-duration ease-in-out);
        }
    }

    .pathway-card__txt {
        color: initial;
        padding: $measure-medium;
        .description {
            margin: 0;
        }
    }

    .pathway-card__txt__heading {
        .heading {
            text-decoration: underline;
        }
    }

    .pathway-card__txt .icon {
        font-size: 18px;
        &::before {
            content: "\f054";
        }
    }
}
