////
/// Highlight info
/// @group components
////

.highlight-info-group {
    padding-top: $measure-medium;
    padding-bottom: $measure-large;
}

.highlight-info {
    padding-top: $measure-medium;
    padding-bottom: $measure-large;
    padding-right: $measure-medium;
}

.highlight-info__lead {
    display: block;
    margin-bottom: $measure-small;
    font-size: $font-size-beta;
    font-family: $font-family-meta;
    @include heading-font-features();
    color: $color-flamingo;
}

.highlight-info__support {
    display: block;
    font-weight: $font-weight-medium;
}

.highlight-info.highlight-info--prussian {
    .highlight-info__lead {
        color: $color-prussian;
    }
}

@mixin highlighted-content-v23 ($color: $color-corporate-accent-2) {
    border-left: $measure--half solid $color;
    margin-bottom: $measure--basic;
    margin-top: $measure--basic;
    padding-bottom: $measure--basic;
    padding-left: $measure--small2;
    padding-top: $measure--basic;
    background-color: $color-white;
    max-width: $max-line-length;

    *:last-child {
        margin: 0;
    }

    .highlighted-content-v23__heading {
        @include h4style();
        @extend %bold;
        margin-bottom: $measure--half;
        max-width: $max-line-length;
    }
}

.highlighted-content-v23 {
    @include highlighted-content-v23;
}

@each $level, $color in $courses-v23-palette {
    $decoration: nth($color, 5);

    .#{$level}-courses-v23 {
        .highlighted-content-v23 {
            @include highlighted-content-v23($decoration);
        }
    }
}

.text-highlight {  // v23
    @include text-highlight();
    line-height: 2.5rem;
}

.entry-req-v23__heading--highlight { //only UG courses have this component
    @include text-highlight($color-black, $color-ug-lead);
    line-height: 2.5rem;
}

@each $level, $color in $courses-v23-palette {
    $text-color: nth($color, 4);
    $bg-color: nth($color, 3);

    .#{$level}-courses-v23 {
        .text-highlight {
            @include text-highlight($text-color, $bg-color);
        }
    }
}
