.student-life-v23__links__unibuddy{
    ul{
        margin-left: initial;
        margin-bottom: initial;

        li{
            max-width: initial;
        }
    }
}

.unibuddy-card-v23 {
    box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    background-color: $color-white;
    display: flex;
    flex-direction: column;
    padding: $measure--small4;    

    .unibuddy-card-v23__content__image {
        display: flex;
        justify-content: center;
        flex-direction: column;

        img {
            width: $measure--large3;
            @include border-radius(50%);
            margin-left: auto;
            margin-right: auto;
        }

        &:after {
            content: '';
            display: block;
            height: $measure--quarter;
            margin-left: auto;
            margin-right: auto;
            margin-top: $measure--small2;
            margin-bottom: $measure--small2;
            width: $measure--medium7;
        }
    }

    .unibuddy-card-v23__content__details {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: $measure--basic;
    }

    .unibuddy-card-v23__content__details__name {
        font-weight: $font-weight-bold;
        @include font-family(meta);
        font-size: $font-size-delta;
    }

}

.unibuddy-card-v23--general {
    .unibuddy-card-v23__content__title {
        font-weight: $font-weight-bold;
        @include font-family(meta);
        font-size: $font-size-delta;
        text-align: center;

        &:after {
            content: '';
            display: block;
            height: $measure--quarter;
            margin-left: auto;
            margin-right: auto;
            margin-top: $measure--small2;
            margin-bottom: $measure--small2;
            width: $measure--medium7;
        }
    }
}

.unibuddy-card-v23__cta__link {
    @include anchor(cta--v23--black);
}

.unibuddy-card-v23__content__image,
.unibuddy-card-v23__content__title {
    &:after {
        background: $color-black;
    }
}
