// General styling
.courses-v23 {
    .course-card-v23__anchor {
        max-width: 100%;

        &:focus {
            background-color: $color-white-smoke;
            outline-offset: $border-width-thick;
            box-shadow: 0 0 0.75rem rgba($color-woodsmoke, 0.3);
        }
    }
    .courses-v23__related-courses__heading {
        
        @include media('>medium') {
            margin-top: $measure--small2;
        }
    }


}
.js {
    .slider-v23--related-courses__wrap {
        position: relative;
        ul {
            gap: $measure--small1;
        }
    }
    
    .course-card-v23__content {
        background-color: $color-white;
        height: 100%;
    }
    .course-card-v23__details {
        padding: $measure--small1;
        color: $color-black;
    }
    
    .course-card-v23__details__title {
        font-weight: $font-weight-bold;
        @include font-family(meta);
        font-size: $font-size-gamma;
        margin-bottom: $measure--basic;
        text-decoration: underline;
        line-height: 1.25;
    }
    
    .course-card-v23__anchor {
        display: block;
        border: none;
        &:after {
            //@include transition(opacity);
            transition: opacity $transition-duration-long;
            content: '';
            opacity: 0;
        }
    
        &:hover {
            background-color: transparent;
            &:after {
                opacity: 1;
            }
        }
    }

    .slider-v23[data-style='responsive'] {
        --slider-min-height: auto; //Default min height, this is overwritten by tallest slide, set by slider JS
        min-height: var(--slider-min-height);
        .course-card-v23 {
            min-height: var(--slider-min-height);
            display: flex;
        }
        & + .slider-v23__controls--responsive {
            .slider-v23__indicator {
                display: none;
            }
        }
    }
}

@each $level, $color in $courses-v23-palette {
    $lead-color: nth($color, 1); // bracket
    $lead-text: nth($color, 2);
    $accent-color: nth($color, 3); // arrows
    $background-color: nth($color, 8);
    $decoration: nth($color, 5);
    $outline-colour: nth($color, 24);

    .#{$level}-courses-v23 {
        .courses-v23__related-courses {
            background-color: $background-color;
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;
            z-index: z-index('root');
            .course-card-v23__anchor {
                &:after {
                    @include corner-border-v23($decoration, 'bottom-right');
                }
            }

            &:after {
                content: '';
                position: absolute;
                height: 100%;
                width: 100vw;
                background-color: $background-color;
                top: 0;
                display: flex;
                z-index: z-index('below');
            }

            &:before {
                content: '';
                position: absolute;
                height: $measure--medium1;
                width: 50vw;
                background-color: $color-white-smoke;
                top: 0;
                display: flex;
                z-index: z-index('above');
                left: 50%;
                @include media('>medium') {
                    height: $measure--medium9;
                }
               
            }

            .courses-v23__related-courses__container {
                width: 100%;
            }
            .slider-v23__cards {
                margin-bottom: $measure--basic;
                margin-left: -$measure-smallish;
            }

            //Apply same padding for responsive slider to single cards. 
            .course-card-v23 {
                padding: $measure-smallish; 
            }
            .slide .course-card-v23 {
                padding:0;
            }

  

            .slider-v23[data-style='responsive'] {
                .course-card-v23__anchor {
                    &:focus {
                        outline-color: $outline-colour;
                    }
                }
                & + .slider-v23__controls--responsive {
                    button {
                        background: $color-white;
                        color: $color-black;
                        width: $measure--medium3;
                        height: $measure--medium3;

                        &:focus {
                            outline-color: $outline-colour;
                            z-index: 2;
                            outline-style: solid;
                        }

                        &:disabled {
                            opacity: 0.5;
                        }
                    }
                }

                .slide {
                    &:focus,
                    &:active {
                        outline-color: $lead-text;
                    }
                }
            }
        }
    }
}
