.footer__back-to-top.back-to-top[data-docked='true'] {
    width: 100%;
    padding-bottom: $measure-small;

    a {
        float: right;
    }

    .back-to-top__button__label {
        @include visually-hidden;
    }

    .progress-circle {
        display: none;
    }
}

.back-to-top[data-docked='false'] {
    @include smalltype();
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end;

    .back-to-top__button__label {
        @include visually-hidden;
    }

    .back-to-top__button__arrow {
        background: $color-gray7;
        border-radius: 50%;
        color: $color-white;
        height: $measure--small4;
        margin-left: 0.625rem;
        padding: $measure--half;
        text-align: center;
        width: $measure--small4;

        &:before {
            font-size: 0.625rem;
        }
    }

    .back-to-top__anchor {
        display: inline;
        opacity: 1;
        overflow: hidden;
        position: fixed;
        text-decoration: none;
        z-index: z-index('above');
        visibility: visible;
        border-bottom: none;
        transition: opacity 1s ease;

        @include media('<between') {
            bottom: $measure--basic;
            right: $measure--basic;
        }

        @include media('>=between') {
            bottom: $measure--threequarters;
            right: $measure--small1;
        }

        &:active {
            outline: $border-width-default $border-style-alternate $color-black;
            background: none;
            border-bottom-width: 0;
        }

        &:focus {
            outline: $border-width-default $border-style-alternate $color-black;
            border-bottom-width: 0;
        }

        &:hover {
            color: $color-black;
            background: none;
            text-decoration: none;
            border-bottom-width: 0;
        }

        &:hover .back-to-top__button svg circle {
            fill: $color-black;
        }

        &:active .back-to-top__button svg circle {
            fill: $color-black;
        }
    }

    .back-to-top__button {
        border-radius: 50%;
        display: flex;
        height: $measure--medium3;
        width: $measure--medium3;

        svg {
            display: block;
            position: absolute;
        }

        svg path {
            fill: none;
            stroke: $color-black;
            stroke-width: 5;
            transition: stroke-dashoffset 0ms linear;
        }

        svg circle {
            fill: $color-gray7;
        }
    }

    .back-to-top__button__arrow {
        background: none;
        border-radius: 50%;
        height: 2.556rem;
        margin: auto;
        padding-top: $measure--half;
        position: relative;
        width: 2.556rem;

        &:before {
            font-size: 0.8125rem;
            display: block;
        }
    }

    .back-to-top__button__arrow__label {
        @include font-family('default');
        vertical-align: top;
        font-size: 0.8rem;
    }

    &[hidden='true'] {
        .back-to-top__anchor {
            opacity: 0;
            visibility: hidden;
        }
    }
}

.back-to-top {
    @include media('print') {
        display: none;
    }
}
