////
/// Content
/// @group base
////

html {
    @include font-family('default');
    @include body-font-features();
    font-size: $font-size--mobile;
    font-style: normal;
    font-weight: $font-weight-text;
    //line-height: 1;
    line-height: $line-height-default;
    //font-feature-settings: 'lnum' 1;

    @include media('>=large', '<1080p') {
        font-size: $font-size--desktop;
    }

    @include media('>=1080p', '<4K') {
        font-size: $font-size--1080p;
    }

    @include media('>=4K', '<8K') {
        font-size: $font-size--4K;
    }

    @include media('>=8K') {
        font-size: $font-size--8K;
    }
}

/**
 * Content Elements
 */
p,
ul,
ol,
dl,
figure,
table,
blockquote {
    @extend %content-elements !optional;
}

/**
 * Heading Elements
 */
h1,
h2,
h3,
h4,
h5,
h6 {
    @extend %heading-elements !optional;
    @include font-family(meta);
    width: 100%;
    display: block;
    flex: 1 0 auto;
    position: relative;
    margin-bottom: $measure-medium;
    font-weight: $font-weight-default;
}

.alpha,
%alpha {
    @include typographic-scale($font-size-alpha);
}
h1 {
    @extend %alpha !optional;
    font-weight: $font-weight-medium;
}

.beta,
%beta {
    @include typographic-scale($font-size-beta);
}
h2 {
    @extend %beta !optional;
    font-weight: $font-weight-medium;
    line-height: #{$line-height-ratio--override--zeta}rem;
}

.gamma,
%gamma {
    @include typographic-scale($font-size-gamma);
}
h3 {
    @extend %gamma !optional;
    font-weight: $font-weight-medium;
}

.delta,
%delta {
    @include typographic-scale($font-size-delta);
}
h4 {
    @extend %delta !optional;
    font-weight: $font-weight-medium;
}

.epsilon,
%epsilon {
    @include typographic-scale($font-size-default);
}
h5 {
    @extend %epsilon !optional;
    font-weight: $font-weight-bold;
}

.zeta,
%zeta {
    @include typographic-scale($font-size-default);
}
h6 {
    @extend %zeta !optional;
    font-weight: $font-weight-bold;
}

p {
    margin: 0 0 #{$line-height-default}em;

    &:empty {
        display: none;
        visibility: hidden;
    }

    &.columns {
        @include media('>large') {
            text-align: justify;
            column-count: 2;
            column-gap: $measure-large-relative;
        }
    }
}

/**
 * Lists
 */
ol,
ul {
    padding: 0;
    margin: 0;
    list-style: none;
}
ol:not([class]),
ul:not([class]),
.default-list {
    list-style-position: outside;
    margin-bottom: $measure-medium;
    margin-left: $measure-medium;

    // List nested in a list
    & & {
        margin-top: $measure-medium;
    }

    li + li {
        margin-top: 0.5em;
    }
}
ul:not([class]),
ul.default-list {
    list-style-type: square;
}
ol:not([class]),
ol.default-list {
    list-style-type: decimal;

    &[reversed] {
        display: flex;
        flex-direction: column-reverse;
    }
}

figure {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 0 $measure-medium;

    figcaption {
        @include font-family(serif);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: $measure-medium;
        font-style: italic;
        font-size: $font-size-small;
        line-height: $line-height-small;
    }

    & > a {
        display: block;
    }

    img {
        width: 100%;
    }
}

/**
 * Quotes
 */
blockquote {
    @include quotes;

    p {
        &:not(:last-of-type) {
            &:after {
                @include visually-hidden;
            }
        }
    }
}

small {
    font-size: $font-size-small;
}

sub,
sup {
    font-size: 0.4em;
    position: relative;
    vertical-align: baseline;
    line-height: 0;
}

sup {
    top: -0.6em;
}

sub {
    bottom: -0.25em;
}
/**
 * Neuter bad editors
 **/
a,
h1,
h2,
h3,
h4,
h5,
h6 {
    b,
    strong {
        font-weight: inherit;
    }
}

iframe[src*='prezi'] {
    display: none !important;
}

/// from web2020
/// 
@mixin body-baseline() {
    @include font-family('default');
    @include body-font-features();
    @include alphatype();
    font-style: normal;
    font-weight: $font-weight-text;

    b,
    strong {
        font-weight: $font-weight-bold;
    }

    i,
    em {
        font-style: italic;
    }
}

@mixin content-style() {
    @include body-baseline();
    max-width: $max-line-length;
}

@mixin heading-style() {
    @include font-family('heading');
    @include heading-font-features();

    b,
    strong {
        font-weight: inherit;
    }

    i,
    em {
        font-style: italic;
    }

    u {
        text-decoration: none;
    }
}

@mixin h6style() {
    @include heading-style();
    @include alphatype();
    font-style: normal;
    font-weight: $font-weight-medium;
    line-height: $line-height-ratio--heading;
   // margin-top: $heading-margin;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

//h6,
.h6-style {
    @include h6style();
}

@mixin h5style() {
    @include heading-style();
    @include betatype();
    font-style: normal;
    font-weight: $font-weight-medium;
  //  margin-top: $heading-margin;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

//h5,
.h5-style {
    @include h5style();
}

@mixin h4style() {
    @include heading-style();
    @include gammatype();
    font-style: normal;
    font-weight: $font-weight-medium;
   // margin-top: $heading-margin;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

//h4,
.h4-style {
    @include h4style();
}

@mixin h3style() {
    @include heading-style();
    @include deltatype();
    font-style: normal;
    font-weight: $font-weight-medium;
   // margin-top: $heading-margin;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

//h3,
.h3-style {
    @include h3style();
}

@mixin h2style() {
    @include heading-style();
    @include epsilontype();
    font-style: normal;
    font-weight: $font-weight-medium;
   // margin-top: $heading-margin;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

//h2,
.h2-style {
    @include h2style();
}

@mixin h1style() {
    @include heading-style();
    @include zetatype();

    @include media('>=tablet', '<=between') {
        @include etatype();
    }

    @include media('>between') {
        @include thetatype();
    }

    font-style: normal;
    font-weight: $font-weight-medium;

    b,
    strong {
        font-weight: $font-weight-medium;
    }
}

//h1,
.h1-style {
    @include h1style();
}
