$banner-themes: (
    corporate: (
            $color-shiraz,
            $color-white,
            $color-alizarin,
            $color-white
    ),
    corporate-alt: (
            $color-shiraz,
            $color-white,
            $color-alizarin,
            $color-white
    ),
    undergraduate: (
            $color-ug-lead,
            $color-black,
            $color-ug-accent,
            $color-black
    ),
    postgraduate: (
            $color-msc-lead,
            $color-white,
            $color-msc-accent,
            $color-white
    ),
    masters: (
            $color-msc-lead,
            $color-white,
            $color-msc-accent,
            $color-white
    ),
    phd: (
            $color-phd-lead,
            $color-black,
            $color-phd-accent,
            $color-white
    ),
    mba: (
            $color-mba-lead,
            $color-black,
            $color-mba-accent,
            $color-white
    ),
    emba: (
            $color-mba-lead,
            $color-black,
            $color-mba-accent,
            $color-white
    ),
    demba: (
            $color-mba-lead,
            $color-black,
            $color-mba-accent,
            $color-black
    ),
    execed: (
            $color-exec-lead,
            $color-white,
            $color-exec-accent,
            $color-white
    ),
    executive-education: (
            $color-exec-lead,
            $color-black,
            $color-exec-accent,
            $color-white
    ),
    online-msc: (
        $color-msc-lead,
        $color-white,
        $color-msc-accent,
        $color-white
    ),
    centres: (
        $color-centres-low,
        $color-centres-low--text,
        $color-centres-bright,
        $color-centres-bright--text,
    ),
    cce: (
        $color-centres-low,
        $color-centres-low--text,
        $color-centres-bright,
        $color-centres-bright--text,
    )
);

@each $theme, $colors in $banner-themes {
    $title-background: nth($colors, 1);
    $title-color: nth($colors, 2);
    $subtitle-background: nth($colors, 3);
    $subtitle-color: nth($colors, 4);
    
    // Gradients for listing, text banners and embedded video banner
    .scp--#{$theme} {
        .banner-vi.banner-vi--theme--corporate.banner-vi--gradient    {
            background-image: linear-gradient(90deg, $title-background 0%, $subtitle-background 100%);
            background-color: $subtitle-background;

            @include media('<=between') {
                &.banner-vi--text {
                    background-image: linear-gradient(180deg, $title-background 0%, $subtitle-background 69%);
                    display: flex;
                }

                &.banner-vi--listing {
                    background-image: linear-gradient(180deg, $title-background 0%, $subtitle-background 46%);
                    display: flex;
                }
            }
        }
    }

    .banner-vi.banner-vi--theme--#{$theme}.banner-vi--gradient {
        background-image: linear-gradient(90deg, $title-background 0%, $subtitle-background 100%);
        background-color: $subtitle-background;

        @include media('<=between') {
            &.banner-vi--text {
                background-image: linear-gradient(180deg, $title-background 0%, $subtitle-background 69%);
                display: flex;
            }

            &.banner-vi--listing {
                background-image: linear-gradient(180deg, $title-background 0%, $subtitle-background 46%);
                display: flex;
            }
        }
    }

    // for embedded video banner only (mobile)
    .scp--#{$theme} .banner-vi--video-embedded.banner-vi--theme--corporate {
        @include media('<=between') {
            .banner-vi__container__gradient {
                background-image: linear-gradient(180deg, $title-background 0%, $subtitle-background 95%);
                background-color: $subtitle-background;
            }
            
            .banner-vi__container__column:nth-child(2) {
                background: $subtitle-background;
            }
        }
    }      
    
    .banner-vi--video-embedded.banner-vi--theme--#{$theme}.banner-vi--gradient {
        @include media('<=between') {
            .banner-vi__container__gradient {
                background-image: linear-gradient(180deg, $title-background 0%, $subtitle-background 95%);
                background-color: $subtitle-background;
            }

            .banner-vi__container__column:nth-child(2){
                background: $subtitle-background;
            }
        }
    } 

    // for listing banner only
    .banner-vi--listing.banner-vi--theme--#{$theme} + main .course-finder__top .container, 
    .banner-vi--listing.banner-vi--theme--#{$theme} + main .expert-finder__top .container {
        background-image: linear-gradient(90deg, $title-background 0%, $subtitle-background 100%);

        @include media('<=between') {
            background: $subtitle-background;
            display: flex;
        }
    }

    // Gerenrally for banner headings
    .scp--#{$theme} {
        .banner-vi.banner-vi--theme--corporate {
            h1 {
                background-color: $subtitle-background;
                color: $title-color;

                @include media('<=between') {
                    text-align: center;
                }
            }
        }

        .banner-vi .banner-vi__shape--left svg path, 
        .banner-vi .banner-vi__shape__mobile--left svg path {
            @if ($theme == "corporate-alt") {
                fill: $color-alizarin;
            }
            @else if ($theme == "corporate") {
                fill: $color-alizarin;
            }
            @else {
                fill: $subtitle-background;
            }
        }

        .banner-vi--video-embedded.banner-vi--theme--corporate figcaption {
            color: $title-color;
        }
    }

    .banner-vi.banner-vi--theme--#{$theme} {
        h1 {
            background-color: $subtitle-background;
            color: $title-color;

            @include media('<=between') {
                text-align: center;
            }
        }

        .banner-vi__shape--left svg path,
        .banner-vi__shape__mobile--left svg path {
            @if ($theme == "corporate-alt") {
                fill: $color-alizarin;
            }
            @else if ($theme == "corporate") {
                fill: $color-alizarin;
            }
            @else {
                fill: $subtitle-background;
            } 
        }
    }

    .banner-vi--video-embedded.banner-vi--theme--#{$theme} figcaption{
        color: $title-color;    
    }

    // Promo colours for image and promo banners
    .scp--#{$theme} {
        .banner-vi.banner-vi--theme--corporate {
            .cta, .promo-cta.promo-cta--small h2::after { 
                background-color: $color-woodsmoke;
            }   
        }
    }

    .banner-vi.banner-vi--theme--#{$theme} {
        .cta, .promo-cta.promo-cta--small h2::after  {
            background-color: $color-woodsmoke;
        }
    }

    // Promo colours for image and promo banners
    @include media('<=between') {
        .scp--#{$theme} {
            .banner-vi.banner-vi--theme--corporate.banner-vi--image--promo {
                .banner-vi__container__column:nth-child(2)::after { 
                    background-color: $subtitle-background;
                }   
            }
        }
    
        .banner-vi.banner-vi--theme--#{$theme}.banner-vi--image--promo {
            .banner-vi__container__column:nth-child(2)::after  {
                background-color: $subtitle-background;
            }
        }
    }
}

.banner-vi {

    &:not(.banner-vi--listing) {
        margin-bottom: $measure-large;
    }

    .banner-vi__container {
        height: 100%;
        @extend .container;
        @include media('<=between'){
            padding-left: initial;
            padding-right: initial;
            display: block;
            margin-left: initial;
            margin-right: initial;
            max-width: initial;
        }
    }

    .banner-vi__content {
        @include media('<=between'){
            padding-left: initial;
            padding-right: initial;
        }
        @include grid-column-map(
            (
                'small': 12,
                'extralarge': 10,
            )
        );
        @include grid-column-push-map(
             (
                 'small': 0,
                 'extralarge': 1,
             )
        );
    }

    .banner-vi__content__text {
        @include media('<=between'){
            padding-top: initial;
            padding-left: initial;
            padding-right: initial;
            margin-bottom: initial;
        }

        h1,
        .banner-vi__content__text__subtitle {
            margin: 0;
            padding: $measure-small $measure-medium;
            width: auto;
            font-size: $font-size-beta;
            @include font-family(meta);
            box-decoration-break: clone;
            
            @include media('>between'){ 
                display: inline;
                line-height: 2;
            }
        }

        h1{
            @include media('<=between'){
                width:100%;
            }
        } 
    }
}

.banner-vi--no-banner {
    margin-bottom: $measure-large * 1.5;
    margin-top: $measure-large * 1.5;
    text-align: center;

    @include media('<=between'){
        margin-top: 0;
    }

    h1 {
        color: $color-white;
        background-color: $color-alizarin;
        padding: $measure-small $measure-medium;
        display: inline;
        font-size: $font-size-beta;
        @include font-family(meta);
        

        @include media('<=between'){
            display: block;
            margin-top: 0;
        }
    }
}

