.bayes-news-v24,
.bayes-events-v24{
    @include media('>small'){
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .cta{
        @include anchor(cta--outline-v23, $color-black);
        border-width: $border-width-thick;

        @include media('<=small'){
            margin-top: $measure--half;
        }
    }
}

.bayes-events-v24__lists{
    .featured-event{
        padding-left: initial;
        padding-right: initial;
    }

    .feature-events__cards__content{
        width: initial; 
        padding-left: initial;
        padding-right: initial;
    }

    .featured-item__content{
        top: initial;
        width: initial;
    }

    @include media('<medium'){
        .feature-events__cards__content{
            margin-bottom: $measure--small4;
        }
    }
}