.courses-v23__summary {
    .ksp-v23-wrapper {
        grid-column: 1 / -1;
    }

    .accordion-v23 {
        grid-column: 1/12;
        margin-bottom: $measure--small4;
    }

    .summary__profiles__wrap {
        margin-left: $measure--small4;
        width: 50%;

        @include media('<between') {
            width: 100%;
        }
    }

    .youtube-embed--for-content,
    figure {
        max-width: $max-line-length;
    }

    .summary__profile {
        border-left-width: $border-width-thick;
        border-left-style: $border-style-default;
        padding-left: $measure--small2;
        display: flex;
        flex-direction: row;

        &:not(:last-child) {
            margin-bottom: $measure--small2;
        }

        .summary__profile__text {
            flex-basis: 70%;
        }

        .summary__profile__text__name {
            margin-bottom: $measure--half;
            font-size: $font-size-gamma;
        }

        .summary__profile__text__title {
            font-size: $font-size-delta;
            font-weight: $font-weight-light;
            color: $color-emperor;
        }

        .summary__profile__img {
            padding-left: $measure--basic;

            img {
                @include border-radius(50%);
                max-width: $measure--large2;
            }
        }
    }
}

@each $level, $color in $courses-v23-palette {
    $left-border: nth($color, 5);

    .#{$level}-courses-v23 {
        .courses-v23__summary {
            .summary__profile {
                border-color: $left-border;
            }
        }
    }
}

.courses-v23__summary {
    @include media('>medium') {
        @include x-column-grid(18);
        display: grid;
    }
    
    .youtube-video__caption-element--text, .youtube-video__caption-element--time {
        font-size: $font-size--mini;
    }
}

.courses-v23__summary__image {
    margin-bottom: $measure--basic;
}

.courses-v23__summary__content {
    grid-column: 1 / 12;
}

.courses-v23__summary__heading {
    grid-column: 1 / -1;
}

.courses-v23__summary__widget,
.courses-v23__summary__image {
    grid-column: 12 / -1;
}

.courses-v23__summary__widget__logos {
    @include media('<=between') {
        margin-bottom: $measure--small4; // 2rem
    }

    li {
        display: inline-block;
    }
    img {
        width: auto;
        height: $measure--medium7; //4rem
        margin-right: $measure--basic;
    }
}

.courses-v23__summary__profiles__extra {
    margin-top: $measure--small4;
}

// MBA specific override
.mba-courses-v23 {
    .courses-v23__summary {
        .accordion-v23 {
            grid-column: 1/12;
        }
    }

    .courses-v23__summary__content {
        grid-column: 1/12;
    }

    .courses-v23__summary__widget,
    .courses-v23__summary__image {
        grid-column: 12/-1;
    }
}