//used in UG, Career Pathways logos
.multiple-item-strip {
    & > div {
        display: inline-block;
        max-width: 120px;
    }
}

//Used in UG, the image when there are no stats
.section--vertical-split {
    background-image: linear-gradient(
        to top,
        $color-apricot 50%,
        $color-white 50%
    );

    img {
        width: 100%;
        height: auto;
    }

    @include media('<=small') {
        display: none;
    }
}

// weird gap on home page under banner. fucked if I know.
.promo-card__wrapper {
    margin-top: -4px;
}

// not sure where to put this...
.inline-accreditation {
    margin-bottom: $measure-large;

    a {
        @include anchor(simple-image);
        display: inline-block;
        margin-right: $measure-medium;
        margin-bottom: $measure-small;
    }

    img {
        max-height: $measure-large;
    }
}

.wFormContainer {
    * {
        font-family: $font-family-sans !important;
        @include media('>=large', '<1080p') {
             font-size: $font-size--desktop !important;
         }
     
         @include media('>=1080p', '<4K') {
             font-size: $font-size--1080p !important;
         }
     
         @include media('>=4K', '<8K') {
             font-size: $font-size--4K !important;
         }
     
         @include media('>=8K') {
             font-size: $font-size--8K !important;
         }
     }
    .supportInfo {
        display: none;
    }

    .wForm .field-hint-inactive .hint {
        //Styles coming from the wForm style sheet doesn't pass WCAG AA contrast tests
        opacity: 1 !important;
        color: $color-lucifer !important;
    }
}

// Temporary UG course CTA button fix (for IE11)
.cta.cta--yellow-inline-block {
    @include anchor(cta--yellow);
    display: inline-block;
    width: 100%;
    &:hover {
        width: 100%;
    }
}

#CybotCookiebotDialog .CybotCookiebotDialogBodyLink,
#CybotCookiebotDialogDetail a {
    border-bottom: none;
    transition: none;

    &:hover {
        background-color: unset;
    }
}
