.courses-v23__learning-methods-icons{
    @include x-column-grid(3);
    display: grid;
    list-style: none;

    @include media('>=small','<between'){
        @include x-column-grid(3);
        grid-row-gap: $measure--threequarters;
    }

    @include media('<small'){
        @include x-column-grid(2);
        grid-row-gap: $measure--basic;
    }

    li{
        display: flex;
        flex-direction: column;
        
        @include media('>small'){
            margin-bottom: $measure--basic;
        }
    }

    .courses-v23__learning-methods-icons--icon{
        height: $measure--medium3;
    }

    .courses-v23__learning-methods-icons--title{
        margin-top: $measure--quarter;
        text-align: center;
        @include alphatype();
        font-weight: $font-weight-medium;
        line-height: $line-height-small;
    }
}