.banner-vi--video {
    position: relative;
    overflow: hidden;

    .banner-vi__container__wrapper {
        position: absolute;
        top: 0;
        bottom: 0;
        width: 100%;
        z-index: z-index(above);
    }

    .banner-vi__container {
        flex-direction: row;
        display: flex;
    }

    .banner-vi__container__column {
        flex-basis: 50%;
        align-items: center;
        display: flex;

        @include media('<=large') {
            &:nth-child(1) {
                flex-basis: 70%;
            }

            &:nth-child(2) {
                flex-basis: 30%;
            }
        }

        @include media('<=between') {
            flex-basis: 100%;
            align-items: center;

            &:nth-child(1) {
                flex-basis: 100%;
            }

            &:nth-child(2) {
                flex-basis: 0%;
            }
        }
    }

    .banner-vi__content {
        display: flex;
        flex-direction: row;
    }

    .banner-vi__content__text {
        padding-top: $measure-xlarge * 2.5;
        padding-bottom: $measure-xlarge * 2.5;
        align-items: center;
        display: flex;
        width: inherit;
        flex-basis: 85%;
        left: -3%;

        @include media('<=large') {
            padding-top: $measure-xlarge * 1.5;
            padding-bottom: $measure-xlarge * 1.5;
        }

        @include media('<=medium') {
            padding-top: $measure-xlarge;
            padding-bottom: $measure-xlarge;
        }

        @include media('<=between') {
            padding: 0;
            flex-basis: 100%;
            left: 0;
        }
    }

    .banner-vi__content__text__item {
        @include media('<=between') {
            width: 100%;
            text-align: center;
        }

        h1 {
            background: none;
            color: $color-white;
            text-shadow: 1px 2px rgba(0, 0, 0, 0.3);
            @extend %alpha;
            @include font-family(meta);
            color: $color-lightgrey;
            font-size: $font-size-bayes-banner;
            font-weight: $font-weight-bold;
            line-height: $line-height-default;
            margin: auto;

            margin-bottom: 1rem;
            display: inline;

            @include media('>large') {
                margin: auto;
            }

            @include media('>medium', '<=large') {
                font-size: $font-size-omicron;
                margin: auto;
            }

            @include media('<=medium') {
                font-size: $font-size-beta;
            }

            @include media('<=small') {
                font-size: $font-size-delta;
            }

            @include media('<=min') {
                font-size: $font-size-default;
            }

            @include media('<=between') {
                padding-left: $measure-small * 5;
                padding-right: $measure-small * 5;
            }
        }
    }

    .banner-vi__shape {
        position: relative;

        @include media('<=between') {
            display: none;
        }
    }

    .banner-vi__shape__mobile__wrapper {
        position: absolute;
        z-index: z-index(above);
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;

        @include media('>between') {
            display: none;
        }
    }

    .banner-vi__shape__mobile {
        position: relative;
        height: 100%;

        &.banner-vi__shape__mobile--left {
            width: auto;
            height: 70%;
            left: 0;

            svg {
                left: $measure-medium;
            }
        }

        &.banner-vi__shape__mobile--right {
            width: inherit;
            height: 100%;

            svg {
                right: 0;
            }
        }
    }

    .banner-vi__shape--left,
    .banner-vi__shape__mobile--left {
        flex-basis: 15%;
        height: inherit;

        svg {
            position: absolute;
            height: 100%;
            width: inherit;

            path {
                fill: $color-alizarin;
            }
        }
    }

    .banner-vi__content--right {
        left: inherit;

        @include media('<=between') {
            flex-direction: column;
            width: auto;
        }
    }

    .banner-vi__shape--right,
    .banner-vi__shape__mobile--right {
        height: 100%;
        width: 100%;

        svg {
            transform: rotate(180deg);
            position: absolute;
            height: 101%;
            width: auto;
            right: 0;

            path {
                fill: $color-white;
            }
        }

        &:after {
            content: '';
            position: absolute;
            background-color: white;
            height: 100%;
            width: 100vw;
            left: 99%;
        }
    }

    .video-banner__controls {
        left: 0;
        right: 0;
        display: flex;
        justify-content: flex-end;
        position: absolute;
        @extend .container;
        @include media('<=between') {
            padding-left: initial;
            padding-right: initial;
            display: block;
            margin-left: initial;
            margin-right: initial;
            max-width: initial;
            text-align: center;
            bottom: $measure-medium;
        }

        .video-banner__play {
            margin-right: 15%;

            @include media('<=between') {
                margin-right: auto;
                margin-left: auto;
                position: relative;
                display: block;

                #play {
                    width: $measure-xlarge;
                    height: $measure-xlarge;
                }
            }
        }
    }

    .bayes-video-banner__banner {
        line-height: 0;

        &:before {
            height: 100%;
        }

        &.playing {
            display: block;
        }
    }
}
