.courses-v23__careers__testimonials {
    grid-column: 1/13;
}




.bracket-border {
    @include bracket-border-v23();
}

@each $level, $color in $courses-v23-palette {
    $lead-color: nth($color, 1);
    $lead-text: nth($color, 2);
    $accent-color: nth($color, 3);


    $outline-colour: nth($color, 24);

    .#{$level}-courses-v23,
    .scp--mba {
        .testimonial-card-v23__image {
            &:before {
                border: $border-width-default-w20
                    $border-style-default
                    $accent-color;
            }
        }
    }

    .js .#{$level}-courses-v23 {

        .bracket-border, .blockquote__wrap{
            border-color: $accent-color;
        }

        .testimonials-v23 {
            
            
            .slider-v23__controls--dots {
                button {
                    border: $border-width-hairline-w20
                        $border-style-default
                        $lead-text;

                    &[aria-expanded='true'] {
                        background-color: $lead-text;
                    }
                }
            }

            .slider-v23__controls--arrows {
                align-items: flex-end;
                .slider-v23__indicator {
                    display: none;
                }
                button {
                    background: $color-black;
                    color: $color-white;
                    width: $measure--medium3;
                    height: $measure--medium3;
                    border: none;
                    &:focus {
                        outline-color: $outline-colour;
                        z-index: z-index(above); //2
                        outline-style: solid;
                    }
    
                    &:disabled {
                        opacity: 0.5;
                    }
                }

            }
        }
    }
}

.testimonials-v23 {
    padding: $measure--small2;

    @include media('<=medium') {
        padding: $measure--basic;
    }

    .slider-v23__wrap {
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        @include media('<=tablet') {
            flex-direction: column;
        }
    }

    .slider-v23__heading {
        margin-bottom: $measure--basic;
    }

    .slider-v23__controls--dots {
        align-self: flex-end;
    }

    .testimonial-card-v23__container {
        display: flex;
        flex-direction: row;
        column-gap: $measure--basic;

        @include media('<=tablet') {
            flex-direction: column-reverse;
        }
    }

    .testimonial-card-v23__text {
        padding: 0 0 0 $measure--threequarters;
        margin-bottom: $measure--small2;

        &:before {
            content: '';
            left: 0;
            position: absolute;
            margin: inherit;
            height: inherit;
            width: inherit;
            background-color: inherit;
            mask: inherit;
        }

        &:after {
            content: '';
            right: auto;
            position: absolute;
            bottom: 0;
        }

        span {
            @include font-family(meta);
        }
    }

    .testimonial-card-v23__image {
        position: relative;
        width: $measure--medium10 + $measure--large2;
        margin-bottom: $measure--basic;
        display: flex;
        flex-direction: row;

        &:before {
            content: '';
            width: $measure--huge1;
            height: 100%;
            position: absolute;
            z-index: z-index(default);
            left: 0;
            top: 0;
        }

        &:after {
            content: '';
            width: $measure--medium1;
            height: 107%;
            position: absolute;
            z-index: z-index(default);
            top: $measure--half;
            right: 0;

            background: radial-gradient(
                ellipse at center,
                $color-white,
                $color-white $border-width-hairline-w20,
                transparent 0,
                transparent 100%
            );
            background-size: $measure--small1 $measure--small1;
        }

        picture {
            width: $measure--huge1;
            top: $measure--half;
            left: $measure--half;
        }

        img {
            width: 100%;
        }
    }

    .testimonial-card-v23__author {
        display: flex;
        flex-direction: column;
        align-content: flex-end;
        flex-wrap: wrap;

        @include media('<=tablet') {
            align-self: flex-start;
            margin-bottom: $measure--basic;
        }

        p {
            margin: 0;
        }
    }

    .testimonial-card-v23__author__name {
        font-weight: $font-weight-bold;
        font-size: $font-size-default;

        a {
            @include font-family(meta);
        }
    }

    .testimonial-card-v23__author__title,
    .testimonial-card-v23__author__label {
        font-size: $font-size--small-089R;
    }
}

.js .courses-v23__careers__testimonials {
    .slider-v23[data-style='dots'] {
        margin: 0 0 $measure--basic 0;

        .slide {
            &[data-sliderposition='-1'] {
                visibility: hidden;
            }

            &[data-sliderposition='1'] {
                visibility: hidden;
            }

            &.testimonial-card-v23 {
                &:focus {
                    outline-color: $color-white;
                }
            }
        }
    }

    .slider-v23__controls--dots {
        padding: 0;

        @include media('<=tablet') {
            align-self: center;
        }

        button {
            margin: $measure--quarter * 1.5;

            &:focus,
            &:active {
                outline-color: $color-white;
            }

            &:last-of-type {
                margin: $measure--quarter * 1.5;
            }

            &[aria-expanded='true'] {
                width: $measure--small3;
                height: $measure--small3;
                margin: $measure--quarter * 0.5;

                &:focus,
                &:active {
                    outline-color: $color-white;
                }
            }
        }
    }
}

// MBA specific override
.mba-courses-v23 {
    .courses-v23__careers__testimonials {
        grid-column: 1/12;
    }
}
